import {BaseLayer,Inherited} from './../../utility/imports'
import global from './../../game-global'
import resource from './../../resources'
import defines from './../../game-defines'
const MaskEffect = ()=>{
  let that = Inherited(BaseLayer());
  that.inheritOn('init',()=>{
    return true;
  });
  // let _mashLayer = new PIXI.Container();
  // let zhezhao = new PIXI.Sprite.fromImage(resource.zhezhao);
  // zhezhao.anchor.set(0.5);
  // _mashLayer.addChild(zhezhao);
  //
  // _mashLayer.rotation = (180*2/Math.PI);
  // _mashLayer.position = {x: defines.Canvas.posX_center ,y: defines.Canvas.posY_center };
  // global.events.on('maskeffect',(state)=>{
  //   console.log('' + state);
  //   if(state === 'Crazy'){
  //     that.node.addChild(_mashLayer);
  //   }else if(state === 'Normal' && _mashLayer.parent === that.node){
  //       that.node.removeChild(_mashLayer);
  //   }else {
  //   }
  // });

  let _maskSprite = new PIXI.Sprite.fromImage(resource.zhezhao);
  // _maskSprite.visible = false;
  _maskSprite.alpha = 0;
  _maskSprite.scale = {
    x: 1.2,
    y: 1.2
  }
  that.node.addChild(_maskSprite);

  let _moveIn = ()=>{
    let _movein = new TWEEN.Tween({alpha: 0})
      .to({alpha: 1},600)
      .onUpdate(function () {
        _maskSprite.alpha = this.alpha;
      })
    _movein.start();
  }

  let _moveOut = (cb)=>{
    let _moveout = new TWEEN.Tween({alpha: _maskSprite.alpha})
      .to({alpha: 0},600)
      .onUpdate(function () {
        _maskSprite.alpha = this.alpha;
      })
      .onComplete(function () {
        if (cb){
          cb()
        }
      })
    _moveout.start();
  }

  global.events.on('maskeffect',(state)=>{
    if (state === 'Crazy'){
      // _maskSprite.visible = true;
      _moveIn();
    }else {
      _moveOut(()=>{
        // _maskSprite.visible = false;
      });
    }
  });


  return that;
};
export default MaskEffect;


