import platformHelp from './../platform-helper'
import defines from './../game-defines'
const AudioControl = ()=>{
  let that = {};

  let _audioList = {};
  let playAudio = (id,cb)=>{
    if (that.hasOwnProperty(id)){
      that[id].play();
      that[id].addEventListener('ended',()=>{
        if(cb){
          cb();
        }
      })
    }else {
      let audio = document.getElementById(id);
      that[id] = audio;
      audio.play();
      audio.addEventListener('ended',()=>{
        if(cb){
          cb();
        }
      })
    }

  }



  that.play = (key,cb)=>{
    if (platformHelp.osVer === 1){
      platformHelp.playSound(defines.musicIosConfig[key]);
    }
    if (platformHelp.osVer === 2){
      platformHelp.playSound(defines.musicConfig[key]);
    }
  }

  that.playBg = (key,cb)=>{
    if (platformHelp.osVer === 1){
      platformHelp.playBg(defines.musicIosConfig[key]);
    }
    if (platformHelp.osVer === 2){
      platformHelp.playBg(defines.musicConfig[key]);
    }

  }
  that.playLoop = (id)=>{
    let pcb = ()=>{
      playAudio(id,()=>{
        if (that[id].loop === false){
          return
        }else {
          pcb();
        }
      });
    }
    pcb();
  }
  that.stop = (id)=>{
    that[id].loop = false;
  }
  // that.update = ()=>{
  //   for (let i = 0 ; i < _audioList.length ; i ++){
  //
  //   }
  // }
  // that.createAudio = (name,id)=>{
  //
  //   if (that.hasOwnProperty(name)){
  //     console.log('have this audio' + name);
  //   }else {
  //     that[name] = document.getElementById(id);
  //   }
  // }
  // that.playAduio = (name)=>{
  //   if (that.hasOwnProperty(name)){
  //     that[name].play();
  //   }else {
  //     console.error('not found this audio' + name);
  //   }
  // }
  // that.pauseAudio = (name)=>{
  //   if (that.hasOwnProperty(name)){
  //     that[name].pause();
  //   }else {
  //     console.error('not found this audio' + name);
  //   }
  // }
  // that.resumeAudio = (name)=>{
  //   if (that.hasOwnProperty(name)){
  //     that[name].play();
  //   }else {
  //     console.error('not found this audio' + name);
  //   }
  // }
  // that.stopAudio = (name)=>{
  //   that.pauseAudio(name);
  //   that[name].currentTime = 0;
  // }
  return that;
}
export default AudioControl;


