import {UIControl,ModelLayer,Inherited,Vec2,Director,ResourceManager} from '../../utility/imports'
import defines from './../../game-defines'
import resources from './../../resources'
import global from './../../game-global'
import PlatformHelper from './../../platform-helper'
import HeadNode from './../../common/head-node'
import GameOverNew from './ui-gameover-new'
import GameOverDataShow from './gameoverlayer-common/gameover-data-show-layer'
let _isAction = false;
let _mask;
const UIGameOver = (spec)=> {
  // console.log('now score=' + spec.score);
  const moveAction_y  = (startPos,endPos,time,node,cb)=>{
    if (_isAction){
      return
    }
    _isAction = true;
    let action = new TWEEN.Tween({y: startPos})
      .to({y: endPos},time)
      .onUpdate(function () {
        node.position.y = this.y;
      })
      .onComplete(function () {
        _isAction = false;
        if (cb){
          cb()
        }
      })
      .easing(TWEEN.Easing.Back.Out);
    action.start();
  };
  const moveAction_x  = (startPos,endPos,time,node,cb)=>{
    if (_isAction){
      return
    }
    //_isAction = true;
    let action = new TWEEN.Tween({x: startPos})
        .to({x: endPos},time)
        .onUpdate(function () {
          node.position.x = this.x;
        })
        .onComplete(function () {
          //_isAction = false;
          if (cb){
            cb()
          }
        })
        .easing(TWEEN.Easing.Back.Out);
    action.start();
  };
  let newRank = 0;
  let that = Inherited(ModelLayer(false));
  that.inheritOn('init', ()=> {
    that.node.interactive = true;
    return true;
  });
  that.scaleChange = (object)=>{
    let _text = new TWEEN.Tween({x: object.scale.x, y: object.scale.y})
        .to({x: 1, y: 1},500)
        .onUpdate(function(){
          object.scale.x = this.x;
          object.scale.y = this.y;
        })
        .onComplete(function(){
        })
        .easing(TWEEN.Easing.Back.Out);
    _text.start();
  };
  that.alphaChange = (object)=>{
    let _text = new TWEEN.Tween({alpha: object.alpha})
        .to({alpha: 1},600)
        .onUpdate(function(){
          object.alpha = this.alpha;
        })
        .onComplete(function(){
        })
        .easing(TWEEN.Easing.Sinusoidal.In);
    _text.start();
  };
  // let gameOverNew = GameOverNew({
  //   selfrank: spec.rank,
  //   maxscore: spec.maxScore
  // });




  let overNode = UIControl.create({
    id: 'overNode',
    component: 'Layout',
    children: [
      {
        id: 'qizhi',
        component: 'Sprite',
        image: resources.img_qizhi,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 115
        }
      },
      {
        id: 'chibang',
        component: 'Sprite',
        image: resources.img_chibang,
        position: {
          x: Director.sharedDirector().screen.right * 0.5 - 120,
          y: Director.sharedDirector().screen.bottom * 0.5 - 222
        }
      },
      {
        id: 'chibang2',
        component: 'Sprite',
        image: resources.img_chibang2,
        position: {
          x: Director.sharedDirector().screen.right * 0.5 + 120,
          y: Director.sharedDirector().screen.bottom * 0.5 - 222
        }
      },
      {
        id: 'he2',
        component: 'Sprite',
        image: resources.img_he2,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 280
        }
      },
      {
        id: 'rankTextLight',
        component: 'Sprite',
        image: resources.img_light1,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 280
        },
        alpha: 0
      },
      {
        id: 'rankRiseText',
        component: 'BMLabel',
        text: 0,
        style: {
          font: '18px rank_number_1'
        },
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 355
        },
        alpha: 0
      },
      {
        id: 'layout',
        component: 'Layout',
        position:{
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 280
        },
        children: [
          {
            id: 'rankPolygon',
            component: 'Sprite',
            image: resources.img_he,
            position: {
              x: 0,
              y: 0
            }
          },
          {
            id: 'zhezhao',
            component: "Layout",
            position: {
              x: 0,
              y: 0
            },
            children: [
              {
                id: 'light',
                component: 'Sprite',
                image: resources.img_yellow_26,
                position: {
                  x: 0,
                  y: 0
                }
              }
            ]
          }
        ]
      },
      {
        id: 'n',
        component: 'Sprite',
        image: resources.icon_wodefenshu,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 305
        }
      },

      {
        id: 'rankNum',
        component: 'BMLabel',
        text: 0 ,
        style: {
          font: '38 rank_number_3'
        },
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 255
        }
      },
      {
        id: 'overLine',
        component: 'Sprite',
        image: resources.gameover1,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 180
        }
      }
    ]
  });
  that.node.addChild(overNode.node);



  let buttonNode = UIControl.create({
    id: 'buttonNode',
    component: 'Layout',
    children: [
      {
        id: 'restart',
        component: 'Button',
        image: resources.bt_replay_normal,
        position: {
          x: Director.sharedDirector().screen.right * 0.5 + 200,
          y: Director.sharedDirector().screen.bottom * 0.5 + 365
        }
      },
      {
        id :'close',
        component: 'Button',
        image: resources.bt_quit_normal,
        position: {
          x: Director.sharedDirector().screen.right * 0.5 - 190,
          y: Director.sharedDirector().screen.bottom * 0.5 + 375
        }
      },
      {
        id: 'checkgift',
        component: 'Button',
        image: resources.bt_view_normal,
        position: {
          x: Director.sharedDirector().screen.right * 0.5 + 10,
          y: Director.sharedDirector().screen.bottom * 0.5 + 360
        }
      }
    ]
  },(button)=>{
    if(button.id === 'restart') {
      allMoveOut();
    }
    if (button.id === 'close'){
      PlatformHelper.endAll();
    }
    if(button.id === 'checkgift'){
      PlatformHelper.jumpToPacket();
    }
  });
  that.node.addChild(buttonNode.node);

  let createMoveNode = (image,num,text)=>{
    let moveNode = UIControl.create({
      id: 'moveNode',
      component: 'Layout',
      position: {
        x: Director.sharedDirector().width  + 100,
        y: Director.sharedDirector().height * 0.5 - 240
      },
      children: [
        {
          id: 'image',
          component: 'Sprite',
          image: image,
          position: {
            x: 0,
            y: 0
          }
        },
        {
          id: 'num',
          component: 'BMLabel',
          style: {
            font: 'gold_number'
          },
          position: {
            x: 55,
            y: -5
          },
          text: num
        },
        {
          id: 'text',
          component: 'Label',
          text: text,
          position: {
            x: 70,
            y: 20
          },
          style: {
            font: '16px Arial',
            fill: 0xffffff
          }
        }
      ]
    })
  that.node.addChild(moveNode.node);
    return moveNode
  };


  let removeNode = ()=>{
    that.removeFromWorld();
      global.events.fire('restartgame');
  };
  let allMoveOut = ()=> {
    let overTweenout = new TWEEN.Tween({y: overNode.node.position.y})
      .to({y: overNode.node.position.y - 450},400)
      .onUpdate(function(){
        overNode.node.position.y = this.y
      });
    // let headTweenout = new TWEEN.Tween({y: _gameoverDataShow.node.position.y})
    //   .to({y: 1200},400)
    //   .onUpdate(function(){
    //     _gameoverDataShow.node.position.y = this.y
    //   });
    // let mainTweenout = new TWEEN.Tween({y: gameOverNew.node.position.y})
    //   .to({y: gameOverNew.node.position.y - 450}, 350)
    //   .onUpdate(function () {
    //     gameOverNew.node.position.y = this.y;
    //   });
    let buttonTweenout = new TWEEN.Tween({y: buttonNode.node.position.y})
      .to({y: buttonNode.node.position.y + 550}, 350)
      .onUpdate(function () {
        buttonNode.node.position.y = this.y
      })
      .onComplete(function(){
        removeNode();
        global.events.fire('gamestartagain');
      });
    overTweenout.start();
    // headTweenout.start();
    // mainTweenout.start();
    buttonTweenout.start();
  };

  // global.playerdata.getRanklistData((data)=>{
  //   newRank = data.self_rank===-1?798:data.self_rank;
    //
    let cutRate = global.playerdata.cutFruitCount / global.playerdata.allFruitCount;

    //  
    let superFruitRate = 1;
    let i = global.playerdata.cutSuperCount;
    if ( i <= 19 && i >=0){
      superFruitRate = 1.1;
    }else if (i >= 20 && i <= 29){
      superFruitRate = 1.2;
    }else if (i >= 30 && i <= 35){
      superFruitRate = 1.3;
    }else if (i >= 36 && i < 39){
      superFruitRate = 1.4;
    }else if ( i >= 39){
      superFruitRate = 1.5;
    }

    // let endScore = Math.round(global.playerdata.totalScore * (cutRate + 1) * superFruitRate);
    // endScore = spec.score;
    // console.log('' + global.playerdata.totalScore);
    // console.log('' + cutRate);
    // console.log('' + i);
    // console.log('' + superFruitRate);
    // console.log('endScore' + endScore );
    // overNode.rankNum.text = ''+ global.playerdata.totalScore;

    // gameOverNew.init();
    // gameOverNew.add2World(that);
  // },1,3);
  var hitsList = global.playerdata.hitsLists;
  var hitCount = 0;
  for (var j = 1;j < hitsList.length; j ++){
    hitCount += hitsList[j];
  }
  moveAction_x(240,Director.sharedDirector().screen.right * 0.5 - 120,500,overNode.chibang,()=>{
    if(global.playerdata.totalScore === 0){
    }else {
      global.events.fire('nodeScale');
      global.events.fire('addEndScore', that, ()=> {
        // Mask(endScore)
      });
      if (hitCount === 0) {
      } else {
        global.events.fire('addEndScore1', that);
      }
      if (superFruitRate === 1) {
      } else {
        global.events.fire('addEndScore2', that)
      }
    }

  });
  moveAction_x(240,Director.sharedDirector().screen.right * 0.5 + 120,500,overNode.chibang2,()=>{

  });

  let updateUI = function () {
  };

  //,

  let Mask = (score)=>{
  let createMask = (angle)=> {
    overNode.zhezhao.node.mask = null;
    overNode.zhezhao.node.removeChild(_mask);
    _mask = null;
    _mask = new PIXI.Graphics();
    _mask.beginFill(0xffffff, 1);
    _mask.arc(0, 0, 100, -90 / (180 / Math.PI), (angle - 90) / (180 / Math.PI), false);
    _mask.lineTo(0, 0);
    _mask.endFill();
    overNode.zhezhao.node.addChild(_mask);
    overNode.zhezhao.node.mask = _mask;
  };

  let action = new TWEEN.Tween({angle: 1})
      .to({angle: 360}, 600)
      .onUpdate(function () {
        createMask(this.angle);
      })
      .onComplete(function () {
        that.alphaChange(overNode.rankRiseText);
      });
  action.start();

  //
  let myScore = new TWEEN.Tween({count: overNode.rankNum.text})
      .to({count: score},100)
      .onUpdate(function(){
        overNode.rankNum.text = Math.round(this.count)
      })
  myScore.start();
}

  // numChange.start();
  let light = new TWEEN.Tween({alpha: overNode.rankTextLight.alpha})
    .to({alpha: 0.8},600)
    .onUpdate(function(){
      overNode.rankTextLight.alpha = this.alpha;
    });
  light.start();

  that.update = ()=>{
    updateUI();
  };

  // var _gameoverDataShow = GameOverDataShow(spec);
  // that.node.addChild(_gameoverDataShow.node);
  
  if(global.playerdata.totalScore === 0) {
  }else {
    global.events.fire('colorbar',that);
  }

  return that;
};
export default UIGameOver;


