import {BaseLayer,Inherited,UIControl,ResourceManager,AnimationFactor,Vec2,Director} from './../utility/imports'
import resources from './../resources'
import defines from './../game-defines'
import Fruit from './fruit'
import CutEffect from './effect/cut-effect'
import global from './../game-global'
import shakeNode from './../common/shake-node'
import timeController from './../common/time-controller'
const accAdd = (arg1,arg2)=>{
  var r1,r2,m;
  try{r1=arg1.toString().split(".")[1].length}catch(e){r1=0}
  try{r2=arg2.toString().split(".")[1].length}catch(e){r2=0}
  m=Math.pow(10,Math.max(r1,r2));
  return (arg1*m+arg2*m)/m;
}
const AddFruitType = {
  Invalide: -1,
  Normal: 1,
  Crazy: 2,
  Freeze: 3,
  Double: 4,
  TimeOut: 5,
  ChangeState:6,
  CutBomb: 7,
  FruitOver: 8,
  Crazying: 9,
  CrazyOver: 10,
  CutBombOver: 11,
  SuperFruit: 12
}
const GameState = {
  Invalide : -1,
  Waiting: 0,
  Running: 1,
  Over: 2
}
const FruitLayer = (spec)=>{
  console.log(' ');
  let that = Inherited(BaseLayer());
  that.inheritOn('init',()=>{
    return true;
  });
  let _fruitNameStrs = ['watermelon','pineapple','dargonfruit','kiwifruit',
    'orange','banana','apple','peach','lemon','pear','strawberry'];
  // if (global.playerdata.bombCount > 0){
  //   _fruitNameStrs.push('bomb');
  // }
  let _addFruitTimeDis = [100,20,20,20,100,120,200];
  let _addTimeDisIndex = 0;
  let _timeCount = 0;
  let _TimeScaleOffset = 1;
  let _addFruitDisTimeScaleOffset = 1;
  let _fruits = [];
  let _addFruitType = AddFruitType.Invalide;
  let _cutEffect = CutEffect(that.node);
  let _state = GameState.Invalide;
  let _allCellCount = global.playerdata.fruitCount;
  _cutEffect.add2World(that.node);
  let _redBagRate = global.playerdata.centsNum/global.playerdata.fruitCount;
  let _bombRate = global.playerdata.bombCount / (global.playerdata.fruitCount - 5);
  console.log('redBagRate=' + _redBagRate);
  let _addedTotalRedBag = 0;
  let _addedTotalBomb = 0;
  let _oldTypeList = [];
  let _nowSuperPower = 0;
  let _totalSuperPower = global.playerdata.fruitCount * 0.8;
  let _superFruitCutCount = 0;
  let initData = ()=>{
    _fruitNameStrs = ['watermelon','pineapple','dargonfruit','kiwifruit','orange','banana','apple','peach','lemon','pear','strawberry'];
    // if (global.playerdata.bombCount > 0){
    //   _fruitNameStrs.push('bomb');
    // }
    _addFruitTimeDis = [60,20,10,10,90,80,120];
    _addTimeDisIndex = 0;
    _timeCount = 0;
    _TimeScaleOffset = 1;
    _addFruitDisTimeScaleOffset = 1;
    _fruits = [];
    _addFruitType = AddFruitType.Invalide;
    _allCellCount = global.playerdata.fruitCount;
    _redBagRate =global.playerdata.centsNum/global.playerdata.fruitCount;
    _bombRate = global.playerdata.bombCount / (global.playerdata.fruitCount - 5);
    global.playerdata.getRedbagCount = 0 ;
    global.playerdata.allGetRedbag = 0;
    global.playerdata.cutBombCount = 0 ;
    global.playerdata.cutFruitCount = 0;
    _addedTotalRedBag = 0;
    _addedTotalBomb = 0;
    _oldTypeList = [];
    _nowSuperPower = 0;
    _totalSuperPower = global.playerdata.fruitCount * 0.8;
    _superFruitCutCount = 0;
    that.superFruit = undefined;
  }
  const _randomType = ()=>{
    let types = ['Normal','Crazy'];
    let index = Math.round(Math.random()* (types.length - 1));
    let state = types[index];
    if (_oldTypeList.length === 0){
      state = types[0];
    }else if(_oldTypeList.length === 2){
      if (_oldTypeList[0] === _oldTypeList[1]){
        if (_oldTypeList[0] === types[0]){
          state = types[1];
          }else {
          state = types[0];
        }
      }
      _oldTypeList.splice(0,1);
    }
    _oldTypeList.push(state);
    console.log('' + state);
    return state;
  }
  let _setFruitState = (state)=>{

    if (_addFruitType === state){
      return
    }

    if (_state === GameState.Running){

    }else {
      return
    }
    _addFruitType = state;
    switch (state){
      case AddFruitType.Normal:
        _TimeScaleOffset = 1;
        _addFruitDisTimeScaleOffset = 1;
        global.events.fire('runlight','Normal');
        global.events.fire('uilayer', 'Normal');
        global.events.fire('maskeffect','Normal');
        global.events.fire('fruitwater','Normal');
        timeController.setTimeout('setNormal',()=>{
          console.log('');
          _setFruitState(AddFruitType.ChangeState);
        },5000);

        break;
      case AddFruitType.Crazy:
        // if (global.playerdata.fruitCount <= 20){
        //   _setFruitState(AddFruitType.Normal);
        //  return;
        // }


        _addFruitDisTimeScaleOffset = 0.2;
        global.events.fire('runlight','Crazy');
        global.events.fire('uilayer', 'Crazy');
        global.events.fire('maskeffect','Crazy');
        timeController.setTimeout('setCrazy',()=>{
          _setFruitState(AddFruitType.Crazying);
        },2000);


        break;
      case AddFruitType.Double:
        _TimeScaleOffset = 1;
        _addFruitDisTimeScaleOffset = 1;
        break;
      case AddFruitType.Freeze:
        _TimeScaleOffset = 0.2;
        break;
      case AddFruitType.TimeOut:
        break;
      case AddFruitType.FruitOver:
        global.events.fire('runlight','Normal');
        global.events.fire('uilayer', 'Normal');
        global.events.fire('maskeffect','Normal');
        global.events.fire('fruitwater','Normal');
        timeController.stopAllTimeCB();
        setTimeout(()=>{
          for (let i = 0 ; i < _fruits.length ; i ++){
            _fruits[i].removeSelf();
          }
            global.events.fire('gameover');
            global.audioControl.play('gameover');
        },20);

        break;
      case AddFruitType.CutBomb:
        global.events.fire('runlight','Normal');
        global.events.fire('uilayer', 'Normal');
        global.events.fire('maskeffect','Normal');
        global.events.fire('fruitwater','Normal');
        // console.log('setState CutBomb' );
        timeController.unsetTimeout('setNormal');
        timeController.unsetTimeout('setCrazy');
        timeController.unsetTimeout('setCrazying');
        timeController.unsetTimeout('setCrazyOver');
        timeController.unsetTimeout('setCutBomb');
        // _setFruitState(AddFruitType.CutBombOver);
        timeController.setTimeout('setCutBomb',()=>{
          _setFruitState(AddFruitType.Normal);
        },1000);

        break;
      case AddFruitType.ChangeState:

        if (global.playerdata.fruitCount <= 20){
          _setFruitState(AddFruitType.Normal);

        }else {
          _setFruitState(AddFruitType[_randomType()]);

        }
        break;
      case AddFruitType.Crazying:
        global.events.fire('fruitwater','Crazy');
        timeController.setTimeout('setCrazying',()=>{
          _setFruitState(AddFruitType.CrazyOver);
        },5000);
        break;
      case AddFruitType.CrazyOver:
        global.events.fire('runlight','Normal');
        global.events.fire('uilayer', 'Normal');
        global.events.fire('maskeffect','Normal');
        global.events.fire('fruitwater','Normal');
        timeController.setTimeout('setCrazyOver',()=>{
          _setFruitState(AddFruitType.ChangeState);
        },1000);
        break;
      case AddFruitType.SuperFruit:
        console.log('');



        global.events.fire('runlight','Normal');
        global.events.fire('uilayer', 'Normal');
        global.events.fire('maskeffect','Normal');
        global.events.fire('fruitwater','Normal');
        // timeController.stopAllTimeCB();

        timeController.unsetTimeout('setNormal');
        timeController.unsetTimeout('setCrazy');
        timeController.unsetTimeout('setCrazying');
        timeController.unsetTimeout('setCrazyOver');
        timeController.unsetTimeout('setCutBomb');


        setTimeout(()=>{


          if (_nowSuperPower >= _totalSuperPower){

          }else {
            _setFruitState(AddFruitType.FruitOver);
            return
          }


          if (that.superFruit === undefined){
            console.log('');
            global.audioControl.play('gamestart');
            let fruit = Fruit({
              image: 'superFruit',
              // scale: Math.round(Math.random()*2 + 8) * 0.1
              scale: 1,
              state: _addFruitType
            });
            fruit.add2World(that.node);
            _fruits.push(fruit);
            that.superFruit = fruit;
          }
        },2000);
        ////  


        break;
      default:
        break;

    }
  }


  that.addFruit = (data)=>{
    console.log('addFruit');

    // if (data === 'superFruit'){
    //   let fruit = Fruit({
    //     image: data,
    //     // scale: Math.round(Math.random()*2 + 8) * 0.1
    //     scale: 1,
    //     state: _addFruitType
    //   });
    //   fruit.add2World(that.node);
    //   _fruits.push(fruit);
    // }
    if (_allCellCount <= 0){
      global.playerdata.residualCount = 0;
      global.playerdata.fruitCount = 0;
      global.playerdata.bombCount = 0;
      _allCellCount = 0;

      _setFruitState(AddFruitType.SuperFruit);
      return;
    }

    // Director.sharedDirector().audio.play('addfruit_0');

    global.audioControl.play('addfruit_0');
    let image = _fruitNameStrs[Math.round(Math.random()*(_fruitNameStrs.length - 1))];
    let fruit = Fruit({
      image: image,
      // scale: Math.round(Math.random()*2 + 8) * 0.1
      scale: 1,
      state: _addFruitType
    });
    fruit.add2World(that.node);
    _fruits.push(fruit);
    _addedTotalRedBag = accAdd(_addedTotalRedBag,_redBagRate);
      // console.log('addToTotalRedBag' + _addedTotalRedBag);
    if (global.playerdata.fruitCount === Math.floor(global.playerdata.allFruitCount * 0.5)){
      console.log('');
      global.audioControl.play('getred_0');
      fruit.bigredbag = true;
    }



    if (_addedTotalRedBag >= 1){
      _addedTotalRedBag = _addedTotalRedBag - 1;
      fruit.redbag = true;
      //////
      global.audioControl.play('getred_0');
      }

      _addedTotalBomb = accAdd(_addedTotalBomb, _bombRate);
      if (_addedTotalBomb >= 1){
        _addedTotalBomb = _addedTotalBomb - 1;
        // global.playerdata.bombCount --;
        console.log('');
        // Director.sharedDirector().audio.play('addfruit_1');
        global.audioControl.play('addfruit_1');
        let fruit = Fruit({
          image: 'bomb',
          // scale: Math.round(Math.random()*2 + 8) * 0.1
          scale: 1
        });
        fruit.add2World(that.node);
        _fruits.push(fruit);

      }
      global.playerdata.fruitCount--;
      if(global.playerdata.fruitCount < 0){
        global.playerdata.fruitCount = 0;
        _fruitNameStrs = ['bomb'];
      }

    _allCellCount --;
  }

  that.touchEnd = ()=>{
    beginTime = Date.now();
  }
  that.touchBegin = ()=>{
    // beginTime = Date.now() + 100;
    // hitsCount = 0;
  }
  let hitsCount = 0;
  let beginTime = 0;
  global.events.on('addcutfruit',()=>{
    _nowSuperPower ++;
    if (hitsCount === 0){
      beginTime = Date.now() + 35;
    }else {
      beginTime += 35;
    }
    hitsCount ++;
  });

  const hitsLogic = function () {
    if (Date.now() > beginTime) {
      if (hitsCount >= 2) {
        // if (hitsCount > 8){
        //   hitsCount = 8;
        // }

        if (hitsCount === 2){
          global.audioControl.play('cuteffect_0');
        }else if(hitsCount === 3){
          global.audioControl.play('cuteffect_1');
        }else {
          global.audioControl.play('cuteffect_2');
        }




        _nowSuperPower += hitsCount;
        console.log( hitsCount + '');

        global.playerdata.totalScore += (hitsCount - 1) * 10;

        global.events.fire('caromCount',hitsCount);////
        global.events.fire('addShow',{
          text: hitsCount + '',
          font: 'combo',
          position: {
            x: Director.sharedDirector().screen.right * 0.5,
            y: 300
          }
        });
      }else if (hitsCount === 1){
        global.events.fire('caromCount',hitsCount);
      }

      hitsCount = 0;
    }
    global.events.fire('uilayerprogressbar',_nowSuperPower / _totalSuperPower);
  }


  that.inheritOn('update',(deltaTime)=>{
    if (_state != GameState.Running){
      return
    }
    for (let i = 0 ; i < _fruits.length ; i ++){
      _fruits[i].move(deltaTime);
      if (_fruits[i].isOver()){
        _fruits.splice(i,1);
      }
    }
    if (_addFruitType === AddFruitType.Normal || _addFruitType === AddFruitType.Crazying){
      if (global.playerdata.fruitCount < 10){
        _setFruitState(AddFruitType.Normal);
      }


      if (_timeCount > _addFruitTimeDis[_addTimeDisIndex] * _addFruitDisTimeScaleOffset){
        _timeCount = 0 ;
        _addTimeDisIndex = Math.round(Math.random() * (_addFruitTimeDis.length - 1 ));
        if (_addFruitType === AddFruitType.SuperFruit){
          return;
        }else {
          that.addFruit();
        }
      }
      _timeCount ++;
    }
    hitsLogic();

  });




  that.touchMove = (data)=>{
   // global.events.fire('debug',JSON.stringify(data.identifier));
    let touchPos = Vec2(data.getLocalPosition(that.node).x,data.getLocalPosition(that.node).y);
    for (let i = 0 ; i < _fruits.length ; i ++){
      let touchVec = Vec2(touchPos.x,touchPos.y);
        _fruits[i].Cut(touchVec);
    }
  }

  global.events.on('cutbomb',()=>{
    _setFruitState(AddFruitType.CutBomb);
    shakeNode.shakeNode(that.node);
  });

  global.events.on('superfuritcut',(pos)=>{
    console.log('+1');
    _superFruitCutCount ++;
    // shakeNode.shakeNode(that.node,1);

    let light = new PIXI.Sprite.fromImage(resources.superFruitLight);
    light.alpha = 1;
    light.scale = {
      x: 1,
      y: 1
    }
    light.rotation = Math.random() * 360 / (180/Math.PI);
    light.anchor.set(0.5,0.5);
    light.zOrder = -10

    light.position = pos;
    that.node.addChild(light);
    let alpchange = new TWEEN.Tween({alpha: 1})
      .to({alpha: 0},60)
      .onUpdate(function(){
        light.alpha = this.alpha
      })
      .onComplete(function(){
        //that.node.removeChild(light);
      })
    let sizechange = new TWEEN.Tween({x: 1,y: 1})
      .to({x: 1.5,y: 1.5},80)
      .onUpdate(function(){
        light.scale.x = this.x;
        light.scale.y = this.y;
      })
      .onComplete(function(){
        alpchange.start()
    })
    sizechange.start();
  });
  global.events.on('superfruitcutover',(data)=>{
    console.log('' + data);

    _setFruitState(AddFruitType.FruitOver);
  });
  global.events.on('flashover',()=>{
    for (let i = 0 ; i < _fruits.length ; i ++){
      _fruits[i].removeSelf();
    }
   // _setFruitState(AddFruitType.ChangeState);
  });
  global.events.on('fruitover',()=>{
    // console.log('');
    _setFruitState(AddFruitType.FruitOver);
  });





  let _setState = (state)=>{
    if (_state === state){
      return
    }
    _state = state;
    switch (state){
      case GameState.Waiting:
        break;
      case GameState.Running:
        // console.log('');
        // RandomGameType();
        _setFruitState(AddFruitType.Normal);
        global.events.fire('gamestate','start');
        break;
      case GameState.Over:
        console.log('fruit layer Over');
        global.events.fire('gamestate','over');

        break;
      default:
        break;
    }
  }
  that.toReady = ()=>{
    _setState(GameState.Waiting);
  }

  that.toStart = ()=>{
    console.log('fruit  start');
    initData();
    _setState(GameState.Running);
  }
  that.toEnd = ()=>{
    _setState(GameState.Over);
  }


  return that ;
}
export  default FruitLayer;


