import {ResourceManager,UIControl,BaseLayer,Inherited} from '../../utility/imports'
import global from '../../game-global'
import defines from '../../game-defines'

const Top = -100;

const Control = (spec)=>{
  let that = {};
  that.node = new PIXI.Container();


  let _font = '48px gift_number';

  if (spec.font){
    _font ='44px ' + spec.font;
  }

  let _mainNode = UIControl.create({
    id: 'mainNode',
    component: 'Layout',
    children: [
      {
        id: 'addredpack',
        component: 'BMLabel',
        text: spec.text,
        style: {
          font: _font
        },
        position: spec.position
      }
    ]
  });
  that.node.addChild(_mainNode.node);



 // _mainNode.hitnum.text = spec + '';

  //that.scaleChange = (object)=>{
  //  let _text = new TWEEN.Tween({x: object.scale.x, y: object.scale.y})
  //    .to(
  //      {x: 2, y: 2},500)
  //    .onUpdate(function(){
  //      object.scale.x = this.x;
  //      object.scale.y = this.y;
  //    })
  //  _text.start();
  //};
  //that.scaleChange(_mainNode.addredpack);
  //that.scaleChange(_mainNode.hitnum);


  that.moveText = (spec,cb)=>{
    // let _text = new TWEEN.Tween({y: spec.position.y})
    //   .to({y: Top},3000)
    //
    //   .onUpdate(function(){
    //     spec.position.y = this.y;
    //     _mainNode.node.alpha -= 0.0015;
    //   })
    //   .onComplete(function(){
    //     if (cb){
    //       cb();
    //     }
    //     // that.node.parent.removeChild(that.node);
    //   })
    // _text.start();


    let a1 = new TWEEN.Tween({scale: 0.6})
      .to({scale: 1},200)
      .onUpdate(function () {
        _mainNode.addredpack.node.scale = {
          x: this.scale,
          y: this.scale
        }
      })
      .onComplete(function () {
        setTimeout(()=>{
          a2.start();
          a3.start();
        },300);
      })
      .easing(TWEEN.Easing.Cubic.In);
    a1.start();
    let a2 = new TWEEN.Tween({y :spec.position.y})
      .to({y: spec.position.y - 20},130)
      .onUpdate(function () {
        spec.position.y = this.y;
      })
      .onComplete(function () {
        if (cb){
          cb();
        }
      }).easing(TWEEN.Easing.Cubic.In);

    let a3 = new TWEEN.Tween({alpha: 1})
      .to({alpha: 0},130)
      .onUpdate(function () {
        spec.alpha = this.alpha;
      })
      .onComplete(()=>{
        if (cb){
          cb();
        }
      });

  }

  return that;
}
const ShowLayer = ()=>{
  let that = Inherited(BaseLayer());
  that.inheritOn('init',()=>{
    console.log('init ui show layer');
    return true;
  });


  let uiControl = (data)=>{
    let _control = Control(data);
    //setTimeout(_control.moveText,800);
    _control.moveText(data,()=>{
      that.node.removeChild(_control.node);
    });
    that.node.addChild(_control.node);
  }


  let leftOrright = 'left'
  global.events.on('addShow',(data)=>{
    if (leftOrright === 'left'){
      leftOrright = 'right';
    }else {
      leftOrright = 'left';
    }
    switch (leftOrright){
      case 'right':

        data.position.x -= 20;
        break;
      case 'left':
        data.position.y += 40;
        break;
      default:

        break;
    }


    console.log('add show =' + JSON.stringify(data));
    uiControl(data);

  });
  return that;
}
export default ShowLayer;


