import global from '../../game-global'
import resources from '../../resources'
import {Vec2} from '../../utility/imports'
const CutEffect = (spec)=>{
  let that = new PIXI.Container();
  that.add2World = (parent)=>{
    parent.addChild(that);
  }

  let addEffect = (data)=>{
    let image = data.image;
    let pos = data.position;
    let direction = data.direction;
    let effect = new PIXI.Sprite.fromImage(resources[(()=>{
      var temp = image + '_e';
      var i = Math.round(Math.random() * 2);
      if (i === 0){

      }else {
        temp += '_'+ i;
      };
      console.log('cut effect temp=' + temp);
      return temp;

    })()]);
    effect.anchor = {x: 0.5,y: 0.5};
    that.addChild(effect);
    effect.position = {
      x: pos.x + Math.random() * 160 - 80,
      y: pos.y + Math.random() * 160 - 80
    };

    effect.rotation = (Math.random() * 360) * Math.PI / 180;
    // effect.rotation = Vec2(0,1).getAngle(direction) + Math.PI/180 * 90;

    let tween1 = new TWEEN.Tween({alpha: 1})
      .to({alpha: 0},1200)
      .onUpdate(function(){
        effect.alpha = this.alpha
      })
      .onComplete(()=>{
        that.removeChild(effect);
      })


    let tween = new TWEEN.Tween({scale: 0.6})
      .to({scale: Math.random() * 0.1 + 1},250)
      .onUpdate(function(){
      effect.scale.set(this.scale);
    })
      .onComplete(()=>{
        tween1.start();
      });
    tween.start();
  }


  global.events.on('cuteffect',(data)=>{
    if (data.image === 'bomb'){
      return
    }
    //for (var i = 0 ; i < Math.round(Math.random() * 1) + 1 ; i ++){
    addEffect(data);
    //}

  });


  return that;
}
export default CutEffect;


