import {UIControl,ModelLayer,Inherited,Director} from '../../utility/imports'
import resources from './../../resources'
import gameglobal from './../../game-global'
import defines from './../../game-defines'
const UIStart = ()=>{
  let that = Inherited(ModelLayer(true));
  that.inheritOn('init',()=>{
    //console.log('init start UI');
    that.node.interactive = true;
    return true;
  });

  let _touch = {};
  let readygo_move = true;
  let ui_move = true;
  let readygo_num = 2;

  let _bg = new PIXI.Sprite.fromImage(resources.bj);
  _bg.anchor = {
    x: 0.5,
    y: 0.5
  };
  _bg.position = {
    x: Director.sharedDirector().screen.right * 0.5,
    y: Director.sharedDirector().screen.bottom * 0.5
  };
  _bg.alpha = 0;
  that.node.addChild(_bg);

  /****************************  UI  *******************************/

  let _fruitNode = UIControl.create({
    id: 'fruitNode',
    component: 'Layout',
    children: [
      {
        id: 'fruit',
        component: 'Sprite',
        image: resources.icon_st_shuiguo,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5
        }
      },{
        id: 'textfruit',
        component: 'BMLabel',
        text: 0,
        style: {
          font: '38.4px rank_number_3',
        },
        position:{
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.65 - 20
        }
      }
    ]
  });
  that.node.addChild(_fruitNode.node);

  //let _bombNode = UIControl.create({
  //  id: 'bombNode',
  //  component: 'Layout',
  //  children: [
  //    {
  //      id: 'bomb',
  //      component: 'Sprite',
  //      image: resources.icon_st_zhadan,
  //      position: {
  //        x: Director.sharedDirector().screen.right - 150,
  //        y: Director.sharedDirector().screen.bottom * 0.5
  //      }
  //    },{
  //      id: 'textbomb',
  //      component: 'BMLabel',
  //      text: 0,
  //      style: {
  //        font: '38.4px rank_number_3',
  //      },
  //      position:{
  //        x: Director.sharedDirector().screen.right - 150,
  //        y: Director.sharedDirector().screen.bottom * 0.65 - 20
  //      }
  //    }
  //  ]
  //});
  //that.node.addChild(_bombNode.node);

  let _itemNode = UIControl.create({
    id: 'itemNode',
    component: 'Layout',
    children: [
      {
        id: 'item',
        component: 'Sprite',
        image: resources.img_st_chuxian,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.top + 200
        }
      }
    ]
  });
  that.node.addChild(_itemNode.node);



  let _beginNode = UIControl.create({
    id: 'beginnode',
    component: 'Layout',
    children: [
      {
        id: 'start1',
        component: 'Sprite',
        image: resources.start1,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.8
        }
      },{
        id: 'start2',
        component: 'Sprite',
        image: resources.start2,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.8
        }
      },{
        id: 'start3',
        component: 'Sprite',
        image: resources.start3,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.8
        }
      }
    ]
  });
  that.node.addChild(_beginNode.node);
  _beginNode.start1.visible = false;
  _beginNode.start2.visible = false;
  _beginNode.start3.visible = false;

  let _readygo = UIControl.create({
    id: 'readygo',
    component: 'Layout',
    children: [
      {
        id: 'reday',
        component: 'Sprite',
        image: resources.reday,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5
        }
      },{
        id: 'go',
        component: 'Sprite',
        image: resources.go,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5
        }
      }
    ]
  });
  that.node.addChild(_readygo.node);
  _readygo.reday.visible = false;
  _readygo.go.visible = false;
  _readygo.reday.scale.set(4);
  _readygo.go.scale.set(4);

  /***************************     ************************/

  let _beginlist = [_beginNode.start3,_beginNode.start2,_beginNode.start1];
  let _beginChange = ()=>{
    setTimeout(()=>{
      _beginlist[0].visible = true;
      _change3.start();
    },500);
    let _change3 = new TWEEN.Tween({alpha: 1})
      .to({alpha: 0},1000)
      .onUpdate(function () {
        _beginNode.start3.alpha = this.alpha;
      })
      .onComplete(function () {
        _beginlist[1].visible = true;
        _change2.start();
      })

    let _change2 = new TWEEN.Tween({alpha: 1})
      .to({alpha: 0},1000)
      .onUpdate(function () {
        _beginNode.start2.alpha = this.alpha;
      })
      .onComplete(function () {
        _beginlist[2].visible = true;
        _change1.start();
      })

    let _change1 = new TWEEN.Tween({alpha: 1})
      .to({alpha: 0},1000)
      .onUpdate(function () {
        _beginNode.start1.alpha = this.alpha;
      })
      .onComplete(function () {
        ui_move = false;
        that.moveOut(_itemNode.node,_beginNode.node,_fruitNode.node);
      })
  }

  /***************************    ************************/

  that.moveIn = (object1,object3)=>{
    let _node1 = new TWEEN.Tween({y: object1.position.y - 700})
      .to({y: object1.position.y},240)
      .onUpdate(function () {
        object1.position.y = this.y;
      })
      //.easing(TWEEN.Easing.Cubic.InOut)
    let _node3 = new TWEEN.Tween({y: object3.position.y + 700})
      .to({y: object3.position.y},240)
      .onUpdate(function () {
        object3.position.y = this.y;
      })
      //.easing(TWEEN.Easing.Back.In)
      .onComplete(function () {
        _beginChange();
        that.node.on('touchend', remove);
        that.node.on('mouseup', remove);
      })
    _node1.start();
    _node3.start();
  }
  that.moveIn(_itemNode.node,_fruitNode.node);

  that.moveOut = (object1,object2,object3)=>{
    let _node1 = new TWEEN.Tween({y: object1.position.y})
      .to({y: object1.position.y - 700},300)
      .onUpdate(function () {
        object1.position.y = this.y;
      })
    let _node2 = new TWEEN.Tween({y: object2.position.y})
      .to({y: object2.position.y + 400},300)
      .onUpdate(function () {
        object2.position.y = this.y;
      })
    let _node3 = new TWEEN.Tween({y: object3.position.y})
      .to({y: object3.position.y + 700},300)
      .onUpdate(function () {
        object3.position.y = this.y;
      })
      .onComplete(function () {
        that.node.removeChild(_itemNode.node,_beginNode.node,_fruitNode.node);
        if(readygo_move) {
          gameglobal.audioControl.play('readygo');
          timeChange();
        }
        readygo_move = false;
      })
    _node1.start();
    _node2.start();
    _node3.start();

  };

  /**************************    *************************/

  _fruitNode.textfruit.text = gameglobal.playerdata.fruitCount.toString();

  /**************************      *******************/
  let remove = function (event) {
    if(ui_move){
      that.moveOut(_itemNode.node,_beginNode.node,_fruitNode.node);
      ui_move  = false;
    }

    if (_touch.hasOwnProperty(event.data.identifier)) {
    }else {
      return;
    }
  }


  /***************************  ready go  ********************/

  that.scaleChange = (object)=>{
    let _text = new TWEEN.Tween({x: object.scale.x, y: object.scale.y})
      .to(
        {x: 1, y: 1},500)
      .onUpdate(function(){
        object.scale.x = this.x;
        object.scale.y = this.y;

      })
      .onComplete(function(){
        object.alpha = 1;
      }).easing(TWEEN.Easing.Bounce.Out);
    _text.start();
  };

  let timeChange = ()=>{
    if (readygo_num === 4){
      gameglobal.audioControl.play('gamestart');
      gameglobal.events.fire('gamestart')
      that.removeFromWorld();
      return;
    }
    if (readygo_num === 3){
      _readygo.reday.visible = false;
      _readygo.go.visible = true;
      that.scaleChange(_readygo.go);
      readygo_num ++;
    }
    if (readygo_num === 2){
      _readygo.reday.visible = true;
      that.scaleChange(_readygo.reday);
      readygo_num ++;
    }

    setTimeout(timeChange,1000);

  };

  return that;
}
export default UIStart;


