import {ResourceManager,AnimationFactor,BaseLayer,Inherited,UIControl,Director} from '../../utility/imports'
import resources from '../../resources'
import global from '../../game-global'
import defines from './../../game-defines'
const FlashAnimate = (spec)=>{
    let that = AnimationFactor.createAnimation({
      type: 'frame',
      anchor: {x: 0.5, y: 0.5},
      animates: {
        idle: {
          pre: 'packet_break',
          start: 0,
          end: 12,
          speed: 60 / 60 * 0.2
        }
      }
    });
  that.node.position = {
    x:spec.position.x,
    y:spec.position.y
  };
  that.node.scale.set(1.2);
  that.node.rotation = spec.rotation;
  return that;
};
const FlashEffect = ()=>{
  let that = Inherited(BaseLayer());
  ResourceManager.load(resources.ani_packet_break,()=>{

  });


  let createFlash = (data)=>{
    let flash = FlashAnimate({
      animate: 'packet_break',
      position: {x: data.position.x,y: data.position.y},
      rotation: data.node.rotation * 2 / Math.PI
    });
    let white = new PIXI.Graphics();
    white.beginFill(0xffffff,1);
    white.lineStyle(4, 0xffffff, 1);
    var size = {width: Director.sharedDirector().screen.right,height: Director.sharedDirector().screen.bottom};
    let position = {x: Director.sharedDirector().screen.right * 0.5,y: Director.sharedDirector().screen.bottom * 0.5};
    white.drawRect(position.x-size.width * 0.6, position.y-size.height * 0.6, size.width * 1.2, size.height * 1.2);
    white.alpha = 0;
    let tween1 = new TWEEN.Tween({alpha: 1})
      .to({alpha: 0},400)
      .onUpdate(function(){
        white.alpha = this.alpha
      });
    let tween = new TWEEN.Tween({alpha: 0})
      .to({alpha: 1},350)
      .onUpdate(function(){
        white.alpha = this.alpha
      })
      .onComplete(function(){
        global.events.fire('flashover');
        setTimeout(function(){
          tween1.start();
        },450)
      });
    let resetPack = UIControl.create({
      id: 'resetPackNode',
      component: 'Layout',
      children: [{
        id: 'resetPack',
        component: 'Sprite',
        position: {x: data.position.x, y: data.position.y - 100},
        image: resources.resetPack
      }]
    });
    setTimeout(function(){
      tween.start();
    },100);
    //tween.start();
    that.node.addChild(white);
    if(global.playerdata.getRedbagCount === 0){
    }else {
      that.node.addChild(resetPack.node);
      that.node.addChild(flash.node);
      global.audioControl.play('redbomb');
    }
    flash.playOnce('idle',()=>{
      if(resetPack.node.parent === that.node){
        that.node.removeChild(resetPack.node);
        that.node.removeChild(flash.node);
      }
    });

    return flash;
  };

  global.events.on('flasheffect',(data)=>{
      createFlash(data);
  });
  return that;
};
export default FlashEffect;


