import {BaseLayer,Inherited,UIControl,Director} from '../../utility/imports'
import gameGlobal from '../../game-global'
import resources from '../../resources'
import RunLight from './ui-runlight'
import platformHelper from './../../platform-helper'
import ProgressBar from './../../common/progress-bar'
const UILayer = ()=>{
  let that = Inherited(BaseLayer());
  that.inheritOn('init',()=>{
    return true;
  });

  // let _bar = new PIXI.Sprite.fromImage(resources.bar);
  // _bar.position ={
  //   x: 0,
  //   y: 0
  // };
  // _bar.scale.set(0.96);
  // that.node.addChild(_bar);

  let _changeScore = 0;


  let _fruitFount = UIControl.create({
    id: 'fruitfont',
    component: 'Layout',
    children: [
      {
        id: 'fruitwater',
        component: 'Sprite',
        image: resources.icon_fruitwater,
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.13
        }
      }
    ]
  });
  that.node.addChild(_fruitFount.node);
  _fruitFount.fruitwater.visible = false;


  let _mainNode = UIControl.create({
    id: 'mainNode',
    component: 'Layout',
    children: [
      {
        id: 'bg',
        component: 'Sprite',
        image: resources.bar,
        anchor: {
          x: 0.5,
          y: 0.5
        },
      scale: {
        x: 0.96,
        y: 0.96
      },
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.top + 45
        }
      },{
        id: 'fruitLabel',
        component: 'BMLabel',
        text: 0,
        style: {
          font: '20px default_number',
        },
        position: {
          x: 130,
          y: 50
        }
      },
      {
        id: 'goldLabel',
        component: 'BMLabel',
        text: '000000',
        style: {
          font: '24px gold_number',
          letterSpacing: 10////
        },
        position: {
          x: Director.sharedDirector().screen.right * 0.5 + 2,
          y: Director.sharedDirector().screen.top + 27
        }
      },
      {
        id: 'redpackLabel',
        component: 'BMLabel',
        text: 0,
        style: {
          font: '20px default_number',
        },
        position: {
          x: Director.sharedDirector().screen.right * 0.73,
          y: 50
        }
      },
      {
        id: 'backButton',
        component: 'Button',
        image: resources.bt_back_normal,
        position: {
          x: Director.sharedDirector().screen.left + 30,
          y: Director.sharedDirector().screen.bottom - 20
        }
      },
      {
        id: 'scoreLabel',
        component: 'BMLabel',
        style: {
          font: 'rank_number_1'
        },
        anchor: {
          x: 1,
          y: 0.5
        },
        position: {
          x: Director.sharedDirector().screen.right - 35,
          y: Director.sharedDirector().screen.top + 110
        },
        text: '0'
      },{
        id: 'score',
        component: 'BMLabel',
        style: {
          font: '18px rank_number_1'
        },
        anchor: {
          x: 1,
          y: 0.5
        },
        position: {
          x: Director.sharedDirector().screen.right - 15,
          y: Director.sharedDirector().screen.top + 113
        },
        text: ''
      }
    ]
  },(button)=>{
    if (button.id === 'backButton'){
      platformHelper.endAll();
    }
  });
  that.node.addChild(_mainNode.node);

  _mainNode.backButton.visible = false;

  let isOff = true;
  let addGoldType = 1;
  let changeType = ()=>{
    if (isOff === true){
      isOff = false;
    }else if(isOff === false) {
      isOff = true;
      addGoldType *= -1;
    }

    setTimeout(()=>{
      changeType();
    },Math.round(Math.random()* 2000) + 2000);
  }
  changeType();




  let _nowtime = 0;
  let updateUI = ()=> {
    _nowtime ++;
    if(_nowtime >= 3){
      _nowtime = 0;
      if(_changeScore < gameGlobal.playerdata.totalScore){
        _changeScore ++;
      }
      if (_changeScore > gameGlobal.playerdata.totalScore){
        _changeScore = gameGlobal.playerdata.totalScore;
      }
      _mainNode.scoreLabel.text = _changeScore;
    }

    if (isOff === false) {
    } else if (isOff === true) {
      gameGlobal.playerdata.gold += addGoldType;
      if (gameGlobal.playerdata.gold <= 0) {
        gameGlobal.playerdata.gold = 0;
      }
    }
    let length = 6 - gameGlobal.playerdata.gold.toString().length;
    let str = '';
    for (let i = 0; i < length; i++) {
      str += '0';
    }
    str += gameGlobal.playerdata.gold;
    _mainNode.goldLabel.text = str;

    if (gameGlobal.playerdata.gold >= 999999) {
      gameGlobal.playerdata.gold = 999999;
      addGoldType = -1;
    }
    if (gameGlobal.playerdata.gold <= 0) {
      gameGlobal.playerdata.gold = 0;
      addGoldType = 1
    }


    _mainNode.fruitLabel.text = gameGlobal.playerdata.fruitCount;
    // _mainNode.fruitLabel.text = gameGlobal.playerdata.totalScore;
    //_mainNode.scoreLabel.text = gameGlobal.playerdata.totalScore + '';
    _mainNode.redpackLabel.text = gameGlobal.playerdata.getRedbagCount;


  }
  //
  // let changeGold = ()=>{
  //   _mainNode.goldLabel.text = Math.round(Math.random()*3000 + gameGlobal.playerdata.totalGoldCount);
  //   setTimeout(()=>{changeGold()},2000);
  // }
  // changeGold();

  let _runLight = RunLight();
  that.inheritOn('update',(deltaTime)=>{
    _runLight.update(deltaTime);
    updateUI();

  })
  _runLight.add2World(_mainNode.node);

  //
  let _progressBar = ProgressBar({
    type: 'circle',
    bg_0: resources.jindutiao_0,
    bg_1: resources.jindutiao_1,
    bg_2: resources.jindutiao_2,
    bg_3: resources.jindutiao_3,
    bg_4: resources.jindutiao_4,
    height: 72
  });
  _progressBar.position = {
    x: Director.sharedDirector().screen.right - 60,
    y: Director.sharedDirector().screen.bottom + 150
  }
  // _progressBar.setPrecent(0.5);
  that.node.addChild(_progressBar);


  gameGlobal.events.on('uilayerprogressbar',(precent)=>{
    _progressBar.setPrecent(precent);
  });




  // let _tag = 2;

  let _isAction = false;
  let _moveDown = ()=>{
    if (_isAction){
      return;
    }
    _isAction = true;
    let _move = new TWEEN.Tween({y: -15})
      .to({y: 104},700)
      .onUpdate(function () {
        _fruitFount.fruitwater.y = this.y;
      })
      .easing(TWEEN.Easing.Back.In)
      .easing(TWEEN.Easing.Bounce.Out)
      .onComplete(function () {
        // _tag = 1;
        _isAction = false;
      })
    _move.start();
  }
  let _moveUp = (cb)=>{
    if (_isAction){
      if (cb){
        cb();
      }
      return;
    }
    _isAction = true;
    let _move = new TWEEN.Tween({y: 104})
      .to({y: -100},700)
      .onUpdate(function () {
        _fruitFount.fruitwater.y = this.y;
      })
      .easing(TWEEN.Easing.Back.In)
      .onComplete(function () {
        // _tag = 2;
        _isAction = false;
        if (cb){
         cb()
        }
      })
    _move.start();
  }


  //// 

  gameGlobal.events.on('uilayer',(state)=>{
    console.log('uilayer state' + state);
    if (state === 'Crazy'){
      _fruitFount.fruitwater.visible = true;
      _moveDown();
      // if (_tag === 2){
      //   _moveDown();
      // }
    }
    if (state === 'Normal'){
      _moveUp(()=>{
        _fruitFount.fruitwater.visible = false;
      });
      // if(_tag === 1){
      //   _moveUp();
      // }
      // setTimeout(()=>{
      //   _fruitFount.fruitwater.visible = false;
      // },1000);
    }
  });

  const moveUpDownButton = (node,posY,time)=>{
    let y = node.position.y;
    let action = new TWEEN.Tween({y: y})
      .to({y: y + posY},time)
      .onUpdate(function () {
        node.position.y = this.y
      })
    action.start();
  }
  gameGlobal.events.on('gamestate',(state)=>{
    switch (state){
      case 'start':
        // moveUpDownButton(_mainNode.backButton.node,-25,100)
        // moveUpDownButton(_mainNode.backButton.node,-50,100)
        runAction(_mainNode.node,0,TWEEN.Easing.Cubic.In,()=>{
          _mainNode.backButton.node.visible = true;
        });
        // runAction(_progressBar,0,)


        runAction(_progressBar,
          Director.sharedDirector().screen.bottom - 60,
          TWEEN.Easing.Cubic.Out);


        break;
      case 'over':
        // moveUpDownButton(_mainNode.backButton.node,50,100)
        _mainNode.backButton.node.visible = false;
        runAction(_mainNode.node,-180,TWEEN.Easing.Cubic.Out,()=>{
          //_mainNode.scoreLabel.text = '0';
        });
        runAction(_progressBar,
        Director.sharedDirector().screen.bottom + 150,
        TWEEN.Easing.Cubic.Out);
        break;
      default:
        break;
    }
  });


  let runAction = function (node,posY,effect,cb) {
    let oldY = node.position.y;
    let action = new TWEEN.Tween({y: oldY})
      .to({y: posY},200)
      .onUpdate(function () {
        node.position.y = this.y;
      })
      .onComplete(function () {
        if (cb){
          cb();
        }
      }).easing(effect);
    action.start();

  }
  gameGlobal.events.on('gamestartagain',()=>{
    runAction(_mainNode.node,0,TWEEN.Easing.Cubic.In,()=>{
      _mainNode.backButton.node.visible = false;
    });
  })
  runAction(_mainNode.node,0,TWEEN.Easing.Cubic.In,()=>{
    _mainNode.backButton.node.visible = false;
  });


  return that;
};
export default UILayer;


