import {BaseLayer,BaseWorld,UIControl,Inherited} from './../utility/imports'
import GameLayer from './gamelayer'
import DebugLayer from './debug-layer'
import uiStart from './ui/ui-start'
import UIGameOver from './ui/ui-gameover'
import global from './../game-global'
import Tips from './ui/ui-tips'
import platformHelper from './../platform-helper'
import defines from './../game-defines'
import CaromCount from './../common/carom-count'
let GameState = {
  Invalide: -1,
  GameStart: 0,
  GameRun: 1,
  GameOver: 2,
  GameReStart:3
}
const GameWorld = ()=>{
  let that = Inherited(BaseWorld());
  that.inheritOn('init',()=>{
    return true;
  });

  let _state = GameState.Invalide;

  let _gameLayer = GameLayer(that);
  _gameLayer.init();
  _gameLayer.add2World(that);



  let _setState  = (state)=>{
    console.log('state=' + state);
    if (state === _state){
      return;
    }
    switch (state){

      case GameState.GameStart:
        console.log("set game start");
          console.log("after init data");
          global.playerdata.fruitCount = 100;
          let _uiStart = uiStart();
          _uiStart.init();
          //that.node.addChild(_uiStart.node);
          _uiStart.add2World(that);
          _gameLayer.toReady();
          global.playerdata.hitsLists =[];
          CaromCount();

        break;
      case GameState.GameRun:
        // console.log('GameRun');
        _gameLayer.startGame();
        break;
      case GameState.GameOver:



        ////
        // _gameLayer.gameOver();




        global.events.fire('openranklayer');


          let _uiGameOver = UIGameOver({
              maxscore: 1,
              score: 100
          });
          _uiGameOver.init();
          _uiGameOver.add2World(that);
        // global.playerdata.getRanklistData((rankdata)=>{
          //
          // let selfRankNum = rankdata.self_rank===-1?798:rankdata.self_rank;
        //   global.playerdata.gameOver((data)=>{
        //     if (data.status === 'ok'){
        //       console.log('self score = ' + data.res.resultCount);
        //       let _uiGameOver = UIGameOver(data.res);
        //       _uiGameOver.init();
        //       _uiGameOver.add2World(that);
        //       console.log('get red bag :' + global.playerdata.getRedbagCount);
        //       if (global.playerdata.getRedbagCount >= 1){
        //         platformHelper.tipPacket();
        //       }
        //     } else {
        //       console.log('');
        //       let _tips = Tips(data.show,()=>{
        //         // global.events.fire('restartgame');
        //         platformHelper.endAll();
        //       });
        //       _tips.init();
        //       _tips.add2World(that);
        //     }
        //   });
        // // });
        // ////
        //
        // _gameLayer.gameOver();

        break;
      case GameState.GameReStart:
        console.log('');
          _setState(GameState.GameStart);

          // global.playerdata.init();
        //   global.playerdata.getGameInfo((success, resp)=>{
        //   global.playerdata.initData();
        //   _setState(GameState.GameStart);
        //   if (!success){
        //     let tip = Tips({
        //       show: JSON.stringify(resp)
        //     },()=>{
        //       platform.endAll();
        //     });
        //     tip.init();
        //     tip.add2World(that);
        //   }
        // });


        break;
      default:
        break;
    }
    _state = state;
  }





  global.events.on('gamestart',()=>{
    _setState(GameState.GameRun);
  });
  global.events.on('gameover',()=>{
    _setState(GameState.GameOver);
  });
  global.events.on('restartgame',()=>{
    _setState(GameState.GameReStart);
  });


  // global.events.on('GameStart',()=>{
  //   _setState(GameState.GameStart);
  // });
  // global.events.on('GameRun',()=>{
  //   _setState(GameState.GameRun);
  // });
  // global.events.on('GameOver',()=>{
  //   _setState(GameState.GameOver);
  // });








  _setState(GameState.GameStart);



  //// 
  global.audioControl.playBg('bgmusic');


  return that;
}
export default GameWorld;


