import {Director} from '../../utility/imports'
import resources from '../../resources'
import global from '../../game-global'
const RunLight = ()=>{
  let that = new PIXI.Container();
  that.add2World = (parent)=>{
    that.parent = parent;
    that.parent.addChild(that);
  }

  let _runLightSpeed = 8000;
  let _nowRunLightSpeed = _runLightSpeed;
  let _lights = [];
  for (let i = 0 ; i < 4 ; i ++){

    let light = new PIXI.Sprite.fromImage(resources['runlight_' + i]);
    that.addChild(light);

    light.anchor = {
      x: 0.5,
      y: 0
    }
    light.position = {
      x: Director.sharedDirector().screen.right * 0.5,
      y: Director.sharedDirector().screen.top
    }
    _lights.push(light);
  }


  let runLightState = (state)=>{
    switch (state){
      case 'Normal':
         _runLightSpeed = 8000;

        break;
      case 'Crazy':
        _runLightSpeed = 2000;

        break;
      default:
        break;
    }
  }
  // let lightOff = (bool)=>{
  //   if (bool){
  //     _lights[1].alpha = 0;
  //   }else {
  //     _lights[1].alpha = 1;
  //   }
  // }

  let lightOn = (index)=>{
    for (let i = 0 ; i < _lights.length ; i ++){
      _lights[i].alpha = 0;
    }
    _lights[index].alpha = 1;

  }
  let _timeCount = 0;
  let _runLightNum = 0 ;
  // let _isLight = true;
  that.update = (deltaTime)=>{

    let secondTime = deltaTime/1000;
    _timeCount += secondTime;

    if (_nowRunLightSpeed < _runLightSpeed){
      _nowRunLightSpeed += 60;
    }else {
      _nowRunLightSpeed -= 60;
    }

    if (_timeCount > _nowRunLightSpeed * secondTime){
      _timeCount = 0 ;
      lightOn(_runLightNum);

      if (_runLightNum >= _lights.length - 1){
        _runLightNum = 0 ;
      }else {
        _runLightNum ++;
      }
    }
  }


  global.events.on('runlight',(state)=>{
    runLightState(state);
  });

  return that;
}
export default RunLight;


