import {BaseLayer,Director,UIControl,Inherited} from './../../utility/imports'
import resources from './../../resources'
const GameOverLayer = function (spec) {
  let that = Inherited(BaseLayer());
  that.inheritOn('init',()=>{
    return true;
  });
  let _mainNode = UIControl.create({
    id: 'mainNode',
    component: 'Layout',
    children: [
      {
        id: 'l1',
        component: 'BMLabel',
        text: '           ',
        style: {
          font: '20px combo'
        },
        anchor: {
          x: 0,
          y: 0.5
        },
        position: {
          x: Director.sharedDirector().screen.right * 0.5 - 100,
          y: Director.sharedDirector().screen.bottom * 0.5 - 110
        },
        children: [
          {
            id: 'nowRank',
            component: 'BMLabel',
            style: {
              font: '25px rank_number_1'
            },
            anchor: {
              x: 0.5,
              y: 0.5
            },
            text: '' + spec.selfrank,
            position: {
              x: 120,
              y: -3
            }

          }
        ]
      },
      {
        id: 'l2',
        component: 'BMLabel',
        text: '           ',
        style: {
          font: '20px combo'
        },
        anchor: {
          x: 0,
          y: 0.5
        },
        position: {
          x: Director.sharedDirector().screen.right * 0.5 - 100,
          y: Director.sharedDirector().screen.bottom * 0.5 - 70
        },
        children: [
          {
            id: 'now',
            component: 'BMLabel',
            text: spec.maxscore.toString(),
            style: {
              font: '25px rank_number_1'
            },
            anchor: {
              x: 0.5,
              y: 0.5
            },
            position: {
              x: 125,
              y: -3
            }
          }
        ]
      }
    ]
  });
  that.node.addChild(_mainNode.node);
  if(spec.selfrank.toString() === '-1'){
    _mainNode.nowRank.text = '   --'
  }
  return that;
}
export default GameOverLayer;


