import global from './../game-global'
const CaromCount =()=>{
  let that = [];
  global.events.on('caromCount',(count)=>{
    if(that[count-1] === undefined){
      that[count-1] = 1;
    }else {
      that[count-1] += 1;
    }

    for (let i = 0; i < count-1; i ++){
      if(that[i] > 0){
        that[i] += 0;
      }else {
        that[i] = 0;
      }
    }
    global.playerdata.hitsLists = that;
  })
  return that;
}
export default CaromCount;


