import {BaseLayer,UIControl,Director,Inherited} from './../../../utility/imports'
import resources from './../../../resources'
import global from './../../../game-global'
import particlefruitjson from './../../effect/particle-fruit-json'
const DataUI = function (spec) {
  var that = {
    id: 'dataUI',
    component: 'Layout',
    position: spec.position,
    children: [
      {
        id: 'bg',
        component: 'Sprite',
        image: spec.bgimage
      },
      {
        id: 'mask',
        component: 'Sprite',
        image: resources.icon_box2,
      },
      {
        id: 'Label',
        component: 'Label',
        style: {
          font: '16px Arial',
          fill: '#ffffff'
        },
        text: spec.num,
        position: {
          x: 0,
          y: 27
        }
      },
      {
        id: 'Label',
        component: 'BMLabel',
        style:{
          font: spec.font
        },
        scale: (()=>{
          if (spec.font === 'rank_number_1'){
            return {
              x: 0.5,
              y: 0.5
            }
          }else {
            return {
              x: 1,
              y: 1
            }
          }
        })(),
        position: {
          x: 0,
          y: 60
        },
        text: spec.labelText
      }
    ]
  };
  return that;
}
const DataUI1 = function (spec) {
  var that = {
    id: 'DataUI1',
    component: 'Layout',
    position: spec.position,
    //i: spec.i,
    children: [
      {
        id: 'icon' + spec.i,
        component: 'Sprite',
        image: spec.icon,
        position: {
          x: -46,
          y: -5
        }
      },
      {
        id: 'label' + spec.i,
        component: 'BMLabel',
        style: {
          font: 'rank_number_4'
        },
        text: spec.label,
        anchor: {
          x: 0,
          y: 0.5
        },
        position: {
          x: -32,
          y: -5
        }
      },
      {
        id: 'best',
        component: 'Label',
        style: {
          font: '16px Arial',
          fill: '#efda51'
        },
        text: ':' + spec.best,
        anchor: {
          x: 0,
          y: 0.5
        },
        position: {
          x: -30,
          y: 28
        }
      },
      {
        id: 'now',
        component: 'Label',
        style: {
          font: '16px Arial',
          fill: (()=>{
            if (spec.now === '0'){
              return '#828282';
            }else {
              return '#ffffff';
            }
          })()
        },
        text: ':' + spec.now,
        anchor: {
          x: 0,
          y: 0.5
        },
        position: {
          x: -30,
          y: 53
        }
      }
    ]
  }
  return that;
}
const GameOverDataShowLayer = function (spec) {
  let that = Inherited(BaseLayer());
  that.inheritOn('init',()=>{

    return true;
  });
  let _emitters = {};
  let elapsed = Date.now();
  that.inheritOn('update',()=>{

    let now = Date.now();
      _emitters[0].update((now - elapsed) * 0.001);
    elapsed = now;
  });
  that.scaleChange = (object)=>{
    let _text1 = new TWEEN.Tween({x: object.scale.x, y: object.scale.y})
        .to({x: 1, y: 1},200)
        .onUpdate(function(){
          object.scale.x = this.x;
          object.scale.y = this.y;
        })
        .onComplete(function(){
        })
        .easing(TWEEN.Easing.Back.Out);
    //_text.start();
    let _text = new TWEEN.Tween({x: object.scale.x, y: object.scale.y})
        .to({x: 1.3, y: 1.3},200)
        .onUpdate(function(){
          object.scale.x = this.x;
          object.scale.y = this.y;
        })
        .onComplete(function(){
          _text1.start();
        })
        .easing(TWEEN.Easing.Back.Out);
    _text.start();
  };
  var _mainNode = UIControl.create({
    id: 'mainNode',
    component: 'Layout',
    children: (()=>{
      var children = [];

      var bgui = {
        id: 'bgui',
        component: 'Layout',
        position: {
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 + 145
        },
        children: [
          {
            id: 'bg',
            component: 'Scale9Sprite',
            image: resources.img_go_black,
            offsetX: 10,
            offsetY: 10,
            tilingWidth: 10,
            tilingHeight: 10,
            width: 420,
            height: 240
          }
        ]
      }


      children.push(bgui);


      var tempresources = [resources.icon_hongbao2,resources.icon_hongbao3,resources.icon_shuiguo2,resources.icon_shuiguo3];
      var datas = [global.playerdata.getRedbagCount,
                    // global.playerdata.centsNum - global.playerdata.getRedbagCount,
                  // global.playerdata.centsNum - global.playerdata.getRealRedbagCount,
                  global.playerdata.lostRedBagCount,
                  global.playerdata.cutFruitCount,
                  global.playerdata.allFruitCount - global.playerdata.cutFruitCount];
      var fontList = ['rank_number_1','rank_number_5','rank_number_1','rank_number_5'];
      var labelText = ['','','',''];
      for (var i = 0 ; i < 4 ; i ++){
        var ui = DataUI({
          bgimage: tempresources[i],
          num: datas[i] + '',
          labelText: labelText[i],
          font: fontList[i],
          position: {
            x: 3 * 100 * -0.5 + i * 100 + Director.sharedDirector().screen.right * 0.5,
            y: Director.sharedDirector().screen.bottom* 0.5 + 80
          }
        });
        children.push(ui);
      }

      var iconlist = [resources.icon_mingzhong,resources.icon_lianji,resources.icon_shiliujic];
      var labelList = ['','',''];
      var hitsList = global.playerdata.hitsLists;
      var hitCount = 0;
      for (var i = 1;i < hitsList.length; i ++){
        hitCount += hitsList[i];
      }
      var superCutCount = global.playerdata.cutSuperCount;
      var superRate = '10%';
      if (superCutCount>=0 && superCutCount <= 19){
        superRate = '10%';
      }else if(superCutCount >= 20 && superCutCount <= 29){
        superRate = '20%'
      }else if(superCutCount >= 30 && superCutCount <= 35){
        superRate = '30%'
      }else if (superCutCount >= 36 && superCutCount < 39){
        superRate = '40%'
      }else if (superCutCount >= 39){
        superRate = '50%';
      }
//       1-19 1.1*/
//
//       20-29 1.2*/
//
//       30-39 1.3*/
//
//       40-49 1.4*/
//
// 50 1.5*/
      var nowList = [Math.round(global.playerdata.cutFruitCount/global.playerdata.allFruitCount * 100) + '%',
        hitCount + '',
        // superRate + ''
        superRate
      ];
      let num = Math.round((spec.maxBuffAddition - 1 ) * 100);
      var bestList = [spec.maxAccuracy + '%',spec.maxCombNum + '',num + '%'];
      for (var i = 0 ; i < 3 ; i ++){
        var ui = DataUI1({
          i: i,
          icon: iconlist[i],
          label: labelList[i],
          now: nowList[i],
          best: bestList[i],
          position: {
            x: 2 * 130 * -0.5 + i * 130 + Director.sharedDirector().screen.right * 0.5,
            y: Director.sharedDirector().screen.bottom * 0.5 + 180
          },

        });
        children.push(ui);
      }
      return children
    })()
  });
  that.node.addChild(_mainNode.node);

  //
  //let isAction = false;
  //global.events.on('addEndScore',(data)=>{
  //  //if (isAction){
  //  //  return
  //  //}
  //  //isAction = true;
  //  //if (_emitters.hasOwnProperty('addEndScore')){
  //  //}else {
  //    _emitters['addEndScore'] = new PIXI.particles.Emitter(
  //        that.node,
  //        [PIXI.Texture.fromImage(resources.star2)],
  //        particlefruitjson.addEndScore);
  //  //}
  //  _emitters['addEndScore'].updateOwnerPos(
  //       2 * 150 * -0.5  + Director.sharedDirector().screen.right * 0.5,
  //       Director.sharedDirector().screen.bottom * 0.5 + 180
  //  );
  //  let action = new TWEEN.Tween({
  //    x: 2 * 150 * -0.5 + Director.sharedDirector().screen.right * 0.5,
  //    y: Director.sharedDirector().screen.bottom * 0.5 + 180
  //  })
  //      .to({
  //        x: Director.sharedDirector().screen.right * 0.5,
  //        y: Director.sharedDirector().screen.bottom * 0.5 - 255
  //      },1200)
  //      .onUpdate(function () {
  //        console.log('addadddaddadadadadadadadadaaaaaaaaaaaaaaaaaaaaaaaaaadadadada')
  //
  //        _emitters['addEndScore'].updateOwnerPos(this.x,this.y);
  //      })
  //      .onComplete(()=>{
  //        //isAction = false;
  //        _emitters['addEndScore'].emit = false;
  //
  //      });
  //  action.start();
  //  _emitters['addEndScore'].emit = true;
  //});
  var hitsList = global.playerdata.hitsLists;
  var hitCount = 0;
  for (var i = 1;i < hitsList.length; i ++){
    hitCount += hitsList[i];
  }
  global.events.on('nodeScale',()=>{
    for(let i = 0 ; i < 3 ; i ++) {
      if(global.playerdata.cutSuperCount < 1 && hitCount != 0){
        for(let j = 0 ; j < 2 ; j ++) {
          that.scaleChange(_mainNode['icon' + j]);
          that.scaleChange(_mainNode['label' + j]);
        }
      }else if(hitCount === 0 && global.playerdata.cutSuperCount < 1){
        that.scaleChange(_mainNode['icon' + 0]);
        that.scaleChange(_mainNode['label' + 0]);
      }
      else {
        that.scaleChange(_mainNode['icon' + i]);
        that.scaleChange(_mainNode['label' + i]);
      }
    }
  });
  return that;
}
export default GameOverDataShowLayer;


