const kKeepHoldTime = 0.5;
const kCloserSpeed = 300;

const VectorLengthSq = function (vector) {
  let lengthSq = (vector.x * vector.x + vector.y * vector.y);
  return lengthSq;
};

const VectorNormalize = function (vector) {
  let length = Math.sqrt(VectorLengthSq(vector) );
  vector.x *= 1 / length;
  vector.y *= 1 / length;
};

const OneBlade = function (spec) {
  let that = {};
  let _tailList = [];
  let _points = [];
  let _rope = null;

  const BladeState = {
    StateMoving : 1,
    StateHold: 2
  };

  let _state = BladeState.StateHold;
  let _holdCD = 0;

  const _setState = (new_state)=> {
    if(_state === new_state) {
      return;
    }

    switch (new_state){
    case BladeState.StateHold:
      _holdCD = kKeepHoldTime;
    default:
      break;
    }

    _state = new_state;
  };

  that._addTailPoint = (pos)=>{


    if(_tailList.length > 0) {
      var lastPos = _tailList[_tailList.length-1];
      let step = _tailList.length >= _points.length ? 5: 1;
      if(Math.abs(lastPos.x - pos.x) < step && Math.abs(lastPos.y - pos.y) < step ) {
        return;
      }
    }

    if(_tailList.length >= 10) {
      _tailList.splice(0,1);
    }

    _tailList.push(pos);

    _setState(BladeState.StateMoving);
  };

  that.init = ()=> {
    for(var i=0; i<10; ++i) {
      _points.push({
        x:0 ,
        y:0
      });
    }

    _rope = new PIXI.mesh.Rope(PIXI.Texture.fromImage('assets/cutfruit/blade.png'), _points);
    //_rope.blendMode = PIXI.BLEND_MODES.LIGHTEN;

    spec.node.addChild(_rope);
  };

  that.removeSelf = ()=>{
    spec.node.removeChild(_rope);
  };

  that.update = (dt)=> {

    switch (_state) {
    case BladeState.StateMoving:
      _shiftPoints();
      _setState(BladeState.StateHold);
      break;
    case BladeState.StateHold:
      let step = dt / 1000;
      _holdCD -= step;
      if (_holdCD <= 0) {
        _tailList = [];
        _rope.visible = false;
        return;
      }
      for (let i = _points.length - 2; i > 0; --i) {
        let lastPoint = _points[i + 1];
        let dir = {
          x: lastPoint.x - _points[i].x,
          y: lastPoint.y - _points[i].y
        };

        if (VectorLengthSq(dir) <= 5) {
          _rope.visible = false;
          break;
        }


        VectorNormalize(dir);

        _points[i].x += dir.x * step * kCloserSpeed;
        _points[i].y += dir.y * step * kCloserSpeed;
      }
      break;
    default:
      break;
  }

  };

  const _shiftPoints = ()=>{

    if(_tailList.length < _points.length) {
      _rope.visible = false;
      return;
    }

    //let cnt = _points.length / _tailList.length;
    //let n= _points.length % _tailList.length;
    //
    //for(let i=0; i<_points.length; ++i) {
    //  for(let j=0; j<cnt; ++j) {
    //    _points[i + j].x = _tailList[i].x;
    //    _points[i + j].y = _tailList[i].y;
    //  }
    //}

    _rope.visible = true;
    let point = _tailList[0];
    for(var i=0; i<_points.length; ++i) {
      if (i>= _tailList.length) {
        _points[i].x = point.x;
        _points[i].y = point.y;
      }
      else {
        point = _tailList[i];
        _points[i].x = point.x;
        _points[i].y = point.y;
      }
    }
  };

  return that;
};

export default OneBlade;


