import {BaseLayer,Inherited,UIControl,ResourceManager,Director} from './../utility/imports'
import FruitLayer from './fruit-layer'
import BladeRender from './blade_render'
import defines from './../game-defines'
import resources from './../resources'
import ParticleLayer from './effect/particle-layer'
import UILayer from './ui/ui-layer'
import FlashEffect from './effect/flash-effect'
import UIShowLayer from './ui/ui-showlayer'
import global from './../game-global'
import UIRankList from './ui/ui-ranklist'
import MaskEffect from './effect/mask-effect'
import Camera from './../common/camera'
const GameState = {
  Invalide: -1,
  Waiting: 0,
  Running: 1,
  Over: 2
}
const GameLayer  = ()=>{
  let that = Inherited(BaseLayer());
  that.inheritOn('init',()=>{
    return true;
  });

  let _state = GameState.Invalide;


  let _bg = new PIXI.Sprite.fromImage(resources.bj);
  that.node.addChild(_bg);
  // _bg.anchor.set(0.5);
  _bg.position = {
    x: 0,
    y: 0
  };
  //todo(yuchenl) : need to screen scale
  _bg.scale.y = 1.2;
  //
  //let director = Director.sharedDirector();
  //_bg.position.y = (window.innerHeight - director.screen.bottom) ;
  //_bg.scale.y = 1/director.screen.scale;
  //console.log("screen scale: " + director.screen.scale);

  //
  let _maskEffect = MaskEffect();
  _maskEffect.init();
  _maskEffect.add2World(that);

  //
  let _camera = Camera({
    viewWidth: Director.sharedDirector().width,
    viewHeight: Director.sharedDirector().height,
    maxWidth: Director.sharedDirector().width,
    maxHeight: Director.sharedDirector().height,
  });
  _camera.init();
  _camera.add2World(that);

  //

  let _fruitLayer = FruitLayer();
  _fruitLayer.init();
  _fruitLayer.add2World(that);

  ///

  let _flashEffect = FlashEffect();
  _flashEffect.init();
  _flashEffect.add2World(that);

  //
  let _uiShow = UIShowLayer();
  _uiShow.init();
  _uiShow.add2World(that);


  ///
  // let _uiRanklist = UIRankList();
  // _uiRanklist.init();
  // _uiRanklist.add2World(that);



  //ui
  let _uiLayer = UILayer();
  _uiLayer.init();
  _uiLayer.add2World(that);

  _camera.addToCamera(_flashEffect.node);
  _camera.addToCamera(_fruitLayer.node);
  _camera.addToCamera(_uiShow.node);
  // _camera.addToCamera(_uiLayer.node);
  _camera.addToCamera(_bg);
  global.events.on('zoomIn',()=>{
    _camera.zoomIn(1.4);
    // _bg.scale = {
    // //   x: 1.5,
    // //   y: 1.5
    // // }
  })
  global.events.on('zoomOut',()=>{
    _camera.zoomOut(1);
    // _bg.scale = {
    //   x: 1,
    //   y: 1.2
    // }
  })
  global.events.on('shakeCamera',()=>{
    _camera.shakeCamera();
  });

  //
  let _particle = ParticleLayer();
  _particle.init();
  _particle.add2World(that);


  that.node.interactive = true;
  let blade = BladeRender(that);
  blade.init();
  that.inheritOn('touchMove',(touchPos)=>{
    _fruitLayer.touchMove(touchPos);
  });
  that.inheritOn('touchBegan',()=>{
    _fruitLayer.touchBegin();
  });
  that.inheritOn('touchEnd',()=>{
    _fruitLayer.touchEnd();
  });

  that.inheritOn('update',(delTime)=>{
    blade.update(delTime);
    TWEEN.update();
  });


  let _setState = (state)=>{
    if (state === _state){
      return
    }
    switch (state){
      case GameState.Waiting:

        break;
      case GameState.Running:
        blade.toStartTouchMove();
        _fruitLayer.toStart();


        break;
      case GameState.Over:
        blade.toEndTouchMove();
        _fruitLayer.toEnd();
        break;
      default:
        break;
    }
    _state = state;
  }


  _setState(GameState.Waiting);


  that.startGame = ()=>{
    console.log('startGame' + _state);
    _setState(GameState.Running);
  }

  that.gameOver = ()=>{




    _setState(GameState.Over);
  }
  that.toReady = ()=>{
    _fruitLayer.toReady();
  }







  return that;
}
export  default GameLayer;


