import {SRequest} from './utility/imports'
import defines from './game-defines'
const EventType = {
  enterWeb: 'enter_web',
  clickStartGame: 'click_start_game',
  beginLoading: 'begin_loading',
  endLoading: 'end_loading'
}
const StatisticHelper = function () {
  let that = {};
  const requestData = function (method,params,cb) {
    SRequest.post(defines.gameHomeURL,method + '?uid=' + that.uid,{},params,data=>{
      console.log('requestData',method);
      if (data.status !== 'ok'){
        console.error('requestData,Failed!',JSON.stringify(data));
      }else {

      }

      if (cb){
        cb(data);
      }

    });
  };
  
  
  that.enterWeb = function (uid) {
    console.log('');
    requestData(defines.URLMethods.behaviorRecord,{
      uid: uid,
      event: EventType.enterWeb
    });
  };
  that.clickStartGame = function (uid) {
    console.log('');
    requestData(defines.URLMethods.behaviorRecord,{
      uid: uid,
      event: EventType.clickStartGame
    });
  };
  that.beginLoading = function (uid) {
    console.log('');
    requestData(defines.URLMethods.behaviorRecord,{
      uid: uid,
      event: EventType.beginLoading
    });
  };
  that.endLoading = function (uid) {
    console.log('');
    requestData(defines.URLMethods.behaviorRecord,{
      uid: uid,
      event: EventType.endLoading
    });
  };
  return that;
}
let statisticHelper = StatisticHelper();
window.statisticHelper = statisticHelper;
export default statisticHelper;


