import {BaseLayer,ResourceManager,Inherited,Director} from '../../utility/imports'
import resources from '../../resources'
import global from '../../game-global'
import particlefruitjson from './particle-fruit-json'
const ParticleLayer = ()=>{
  let that = Inherited(BaseLayer());
  that.inheritOn('init',()=>{

    return true;
  });
  let _emitters = {};
  let elapsed = Date.now();
  that.inheritOn('update',()=>{

      let now = Date.now();
      for (let i in _emitters){
        _emitters[i].update((now - elapsed) * 0.001);
      }
      elapsed = now;
  });


  global.events.on('fruitemit',(data)=>{
    if (data.image === 'bomb'){
      return;
    }
    if (_emitters.hasOwnProperty(data.image)){
      _emitters[data.image].updateOwnerPos(data.pos.x,data.pos.y);
      _emitters[data.image].emit = true;
    }else {
      _emitters[data.image] = new PIXI.particles.Emitter(
        that.node,
        [PIXI.Texture.fromImage(resources[data.image + '_pe'])],
        particlefruitjson.fruit);
      _emitters[data.image].updateOwnerPos(data.pos.x,data.pos.y);
    }
  });
  let _isAction = false;
  global.events.on('addredbageffect',(data)=>{
    if (_isAction){
      return
    }
    _isAction = true;
    console.log('addredbageffect' + JSON.stringify(data));
    if (_emitters.hasOwnProperty(data.image)){
    }else {
      _emitters[data.image] = new PIXI.particles.Emitter(
        that.node,
        [PIXI.Texture.fromImage(resources.icon_gift2),PIXI.Texture.fromImage(resources.star)],
        particlefruitjson.redbag);

    }
    _emitters[data.image].updateOwnerPos(
      data.position.x,
      data.position.y
    );
    let action = new TWEEN
      .Tween({x: data.position.x,y: data.position.y})
      .to({x: Director.sharedDirector().screen.right - 10,
        y: Director.sharedDirector().screen.top },1200)
      .onUpdate(function () {
        _emitters[data.image].updateOwnerPos(this.x,this.y);


      })
      .onComplete(()=>{
        _isAction = false;
        _emitters[data.image].updateOwnerPos(
          Director.sharedDirector().screen.right * 0.5,
          Director.sharedDirector().screen.bottom * 0.5
        );
      });
    action.start();
    _emitters[data.image] .emit = true;

    global.events.on('cutbomb',()=>{
      ////  
      action.stop();
      _emitters[data.image].emit = false;
      _isAction = false;
      _emitters[data.image].updateOwnerPos(
        Director.sharedDirector().screen.right * 0.5,
        Director.sharedDirector().screen.bottom * 0.5
      );
    });
  });


  global.events.on('fruitwater',(state)=>{
    console.log('' + state);
    if (_emitters.hasOwnProperty('fruitwater')){
    }else {
      _emitters['fruitwater'] = new PIXI.particles.Emitter(
        that.node,
        [PIXI.Texture.fromImage(resources.star2)],
        particlefruitjson.fruitWater);
    }
    _emitters['fruitwater'].updateOwnerPos(
      Director.sharedDirector().screen.right * 0.5,
      Director.sharedDirector().screen.bottom
    );

    if (state === 'Crazy'){
      _emitters['fruitwater'].emit = true;

    }
    if (state === 'Normal'){
      _emitters['fruitwater'].emit = false;
    }
  })

  global.events.on('colorbar',(layer)=>{
    //if (_emitters.hasOwnProperty('colorbar')){
    //}else {
      _emitters['colorbar'] = new PIXI.particles.Emitter(
        layer.node,
        [PIXI.Texture.fromImage(resources.ribbon_1),PIXI.Texture.fromImage(resources.ribbon_25),PIXI.Texture.fromImage(resources.ribbon_3),PIXI.Texture.fromImage(resources.ribbon_4),PIXI.Texture.fromImage(resources.ribbon_5)],
        particlefruitjson.colorbar);
    //}
    _emitters['colorbar'].updateOwnerPos(
      Director.sharedDirector().screen.right * 0.5 - 50,
      Director.sharedDirector().screen.top + 10
    );
    _emitters['colorbar'].emit = true;
  });

  let isAction = false;
  global.events.on('cutfruit',(data)=>{
    if (isAction){
      return
    }
    isAction = true;
    if (_emitters.hasOwnProperty('cutfruit')){
    }else {
      _emitters['cutfruit'] = new PIXI.particles.Emitter(
        that.node,
        [PIXI.Texture.fromImage(resources.grain)],
        particlefruitjson.cutfruit);
    }
    _emitters['cutfruit'].updateOwnerPos(
      data.position.x,
      data.position.y
    );
    let action = new TWEEN
      .Tween({x: data.position.x,y: data.position.y})
      .to({x: Director.sharedDirector().screen.right - 50 ,
        y: Director.sharedDirector().screen.bottom - 50 },1200)
      .onUpdate(function () {
        _emitters['cutfruit'].updateOwnerPos(this.x,this.y);
      })
      .onComplete(()=>{
        isAction = false;
        _emitters['cutfruit'].emit = false;
      });
    action.start();
    _emitters['cutfruit'].emit = true;
  });
  global.events.on('addEndScore',(layer,cb)=>{

      _emitters['addEndScore'] = new PIXI.particles.Emitter(
          layer.node,
          [PIXI.Texture.fromImage(resources.star2)],
          particlefruitjson.addEndScore);

    _emitters['addEndScore'].updateOwnerPos(
         2 * 150 * -0.5 + Director.sharedDirector().screen.right * 0.5,
         Director.sharedDirector().screen.bottom * 0.5 + 180
    );
    let action = new TWEEN.Tween({
        x: 2 * 150 * -0.5 + Director.sharedDirector().screen.right * 0.5,
        y: Director.sharedDirector().screen.bottom * 0.5 + 180
      })
          .to({
            x: Director.sharedDirector().screen.right * 0.5,
            y: Director.sharedDirector().screen.bottom * 0.5 - 275 },1000)
        .onUpdate(function () {
          _emitters['addEndScore'].updateOwnerPos(this.x,this.y);
        })
        .onComplete(()=>{
          if(cb){
            cb()
          }
          _emitters['addEndScore'].emit = false;
        });
    action.start();
    _emitters['addEndScore'].emit = true;
  });
  global.events.on('addEndScore1',(layer,cb)=>{
    //if (isAction){
    //  return
    //}
    //isAction = true;
    //if (_emitters.hasOwnProperty('addEndScore1')){
    //}else {
      _emitters['addEndScore1'] = new PIXI.particles.Emitter(
          layer.node,
          [PIXI.Texture.fromImage(resources.star2)],
          particlefruitjson.addEndScore);
    //}
    _emitters['addEndScore1'].updateOwnerPos(
         2 * 130 * -0.5 + 130 + Director.sharedDirector().screen.right * 0.5,
         Director.sharedDirector().screen.bottom * 0.5 + 180
    );
    let action = new TWEEN.Tween({
      x: 2 * 130 * -0.5 + 130 + Director.sharedDirector().screen.right * 0.5,
      y: Director.sharedDirector().screen.bottom * 0.5 + 180
    })
        .to({
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 275 },1000)
        .onUpdate(function () {
          _emitters['addEndScore1'].updateOwnerPos(this.x,this.y);
        })
        .onComplete(()=>{
          if(cb){
            cb()
          }
          _emitters['addEndScore1'].emit = false;
        });
    action.start();
    _emitters['addEndScore1'].emit = true;
  });
  global.events.on('addEndScore2',(layer)=>{
    //if (isAction){
    //  return
    //}
    //isAction = true;
    //if (_emitters.hasOwnProperty('addEndScore2')){
    //}else {
      _emitters['addEndScore2'] = new PIXI.particles.Emitter(
          layer.node,
          [PIXI.Texture.fromImage(resources.star2)],
          particlefruitjson.addEndScore);
    //}
    _emitters['addEndScore2'].updateOwnerPos(
         2 * 130 * -0.5 + 2 * 130 + Director.sharedDirector().screen.right * 0.5,
         Director.sharedDirector().screen.bottom * 0.5 + 180
    );
    let action = new TWEEN.Tween({
      x: 2 * 130 * -0.5 + 2 * 130 + Director.sharedDirector().screen.right * 0.5,
      y: Director.sharedDirector().screen.bottom * 0.5 + 180
    })
        .to({
          x: Director.sharedDirector().screen.right * 0.5,
          y: Director.sharedDirector().screen.bottom * 0.5 - 275 },1000)
        .onUpdate(function () {
          _emitters['addEndScore2'].updateOwnerPos(this.x,this.y);
        })
        .onComplete(()=>{
          //isAction = false;
          _emitters['addEndScore2'].emit = false;
        });
    action.start();
    _emitters['addEndScore2'].emit = true;
  });
  return that;
}
export default ParticleLayer;


