import {Director,Vec2} from './../utility/imports'
import resources from './../resources'
import global from './../game-global'
const FruitCell = (spec)=>{
  let that = {};
  let _cells = [];
  let _superCells = [];
  let a = 0;
  that.add2World = (parent)=>{
    that.parent  = parent;

    if (spec.image === 'pomegranate'){

      console.log('');
      // let sp = PIXI.Sprite.fromImage(resources.fasheguang0);
      // that.parent.addChild(sp);
      // sp.anchor.set(0.5);
      that.lowNode = new PIXI.Container();
      that.parent.addChild(that.lowNode);

       that.guang = PIXI.Sprite.fromImage(resources.bg_guang);
      that.guang.anchor.set(0.5);
      that.lowNode.addChild(that.guang);
      var action = new TWEEN.Tween({angle: 0})
        .to({angle: Math.PI * 2},10000)
        .onUpdate(function () {
          that.guang.rotation = this.angle
        })
      action.start();

        for (let i = 0 ; i <6 ; i ++){
          let cell = PIXI.Sprite.fromImage(resources['pomegranatefragments_' + (i + 1)]);
          that.parent.addChild(cell);
          cell.anchor.set(0.5);
          cell.position = spec.position;
          _superCells.push(cell);
        }
      // let sp = PIXI.Sprite.fromImage(resources.fasheguang0);
      // that.lowNode.addChild(sp);
      // sp.anchor.set(0.5);


    }else {
      for (let i = 0 ; i < 4 ; i ++){
        let str = i;
        let cell = new PIXI.Sprite.fromImage(resources[spec.image + '_' + str]);
        that.parent.addChild(cell);
        cell.anchor.set(0.5);
        cell.position = spec.position;
        _cells.push(cell);

        if (spec.state === 9){

        }else {
          if (i < 2){
          }else {
            cell.visible = false;
          }

        }
    }




    }
  }
  global.events.on('cellMove',(data)=> {
    if (spec.image === 'pomegranate'){

    }else {
      return
    }
    a++;
    if (a === 7) {
      _superCells[5].position = {x: a*0.3, y: a*0.3};
      _superCells[4].position = {x: -a*0.3, y: 0};
      _superCells[3].position = {x: -a*0.3, y: a*0.3};
      _superCells[2].position = {x: a*0.3, y: 0}
    }
    else if(a === 14){
      _superCells[5].position = {x: a*0.6, y: a*0.6};
      _superCells[4].position = {x: -a*0.6, y: 0};
      _superCells[3].position = {x: -a*0.6, y: a*0.6};
      _superCells[2].position = {x: a*0.6, y: 0}
    }
    else if(a === 21){
      _superCells[5].position = {x: a*0.75, y: a*0.75};
      _superCells[4].position = {x: -a*0.75, y: 0};
      _superCells[3].position = {x: -a*0.75, y: a*0.75};
      _superCells[2].position = {x: a*0.75, y: 0}
    }else {
      return;
    }
  })



  const moveCell = (node,pos)=>{
    let action1 = new TWEEN.Tween({x:0,y:0})
      .to({x: pos.x,y: pos.y},1000)
      .onUpdate(function(){
        // _cells[i].position = {
        //   x: this.x,
        //   y: this.y
        // }
        node.position = {
          x: this.x,
          y: this.y
        }
      }).onComplete(()=>{
        that.parent.removeChild(node);
      });
    action1.start();

    let angle = Math.random() * 6000 - 3000;;
    let action2 = new TWEEN.Tween({angle: 0})
      .to({angle:angle},5000)
      .onUpdate(function(){
        // _cells[i].rotation = this.angle * Math.PI/180;
        if (spec.image === 'pomegranatefragments'){

        }else {
          node.rotation = this.angle * Math.PI/180;

        }
      })
    action2.start();
  }
  that.cut = ()=>{

    let randomDirection = Vec2(Math.random(),Math.random());
    let cells = [];
    if(spec.image === 'pomegranate'){
      console.log('' + _superCells.length);
      cells = _superCells;
    }else {
      cells = _cells;
      for (let i = 0 ; i < 2 ; i ++){
        _cells[i + 2].visible = false;
      }
    }
    console.log('length = ' + cells.length);
    for (let i = 0 ; i < cells.length ; i ++){
      let direction = randomDirection.rotateByAngle(Vec2(0,0),360 / cells.length * i);
      console.log('direction=' + JSON.stringify(direction));
      let pos = direction.getNormal().multValue(Math.random() * 300 + 300);
      console.log('pos =' + JSON.stringify(pos));
      // moveCell(cells[i],pos);
      if (spec.image === 'pomegranate'){
        moveSuperFruit(cells[i],pos);
      }else {
        moveCell(cells[i],pos);
      }
    }
  }
  const moveSuperFruit = (node, pos)=>{
    //console.log('node.pos' + JSON.stringify(node.position));
    //console.log('target pos' + JSON.stringify(pos));
    let action1 = new TWEEN.Tween({x: 0,y: 0})
      .to({x: pos.x,y: pos.y},600)
      .onUpdate(function () {
        node.position = {
          x: this.x,
          y: this.y
        }
      })
      .onComplete(function () {
        that.parent.removeChild(node);
      }).easing(TWEEN.Easing.Quadratic.InOut);


    action1.start();

  }
  that.removeSelf = ()=>{
    for (let i = 0 ; i < _cells.length; i ++){
      that.parent.removeChild(_cells[i]);
    }
    a = 0;
  }
  return that;
}
export default FruitCell;


