import {Director,Vec2} from './../utility/imports'
import resources from './../resources'
import FruitCell from './fruit-cell'
import global from './../game-global'
import BombCell from './bomb-cell'
import defines from './../game-defines'
const FruitState = {
  Invalide: -1,
  Waiting: 1,
  Running: 2,
  Cut: 3,
  CutOver: 4,
  Over:5,
  SlowSpeed: 7,
  StopY: 8,
  SuperFruitBomb: 9

};

const Fruit = (spec)=>{
  let that = new PIXI.Container();
  that.type = spec.image;
  that.position = {
     x: Director.sharedDirector().screen.right * 0.5 + Math.random()* 100 - 50,
     y: Director.sharedDirector().screen.bottom + 60
  }

  let _state = FruitState.Invalide;
  let _timeScaleOffset = 1;
  let _isCanCut = false;
  let _superFruitCutCount = 0;
  let _isCut = false;
  const _setState = function (newState,callBack) {
    if( _state === newState) {
      return;
    }

    switch (newState) {
      case FruitState.Waiting:
        // idle animation
        //_animation.playOnce('idle',callBack);
        break;
      case FruitState.Running:
        // play cut animation
        _timeScaleOffset = 1;
        break;
      case FruitState.Over:
        // console.log('FruitState Over');
       that.destory();
        break;
      case FruitState.Cut:
        _fruitCell.cut();
        setTimeout(()=>{
          if (that.type === 'bomb'){
          }else {
            _setState(FruitState.CutOver);
          }
        },200);
        break;
      case FruitState.CutOver:
        _RotationSpeed = 0;
        _speedY = 0;
        break;
      case FruitState.SlowSpeed:
        _timeScaleOffset = 0.1;
        break;
      case FruitState.StopY:
        // _timeScaleOffset = 0.1;
        _isCanCut = true;
        global.events.fire('zoomIn');
        _fruitCell.lowNode.removeChild(_fruitCell.guang);

        break;
      case FruitState.SuperFruitBomb:
        // console.log('');
        // global.events.fire('superfruitcutover');
        // setTimeout(()=>{
        //   global.events.fire('superfruitcutover');
        // },1000);
        console.log('position=' + JSON.stringify(that.position));
        shake(that,2,()=>{
          console.log('');
          global.events.fire('zoomOut')
          // global.events.fire('superfruitcutover');
          // that.lowNode.visible = false;
          _fruitCell.lowNode.visible = false;
          _fruitCell.cut();
          global.events.fire('fruitemit',{
            pos: Vec2(that.position.x,that.position.y),
            image: 'pomegranatefragments'
          });
          global.events.fire('superfruitcutover',_superFruitCutCount);

        });
        let sp = PIXI.Sprite.fromImage(resources.fasheguang0);
        _fruitCell.lowNode.addChild(sp);
        sp.anchor.set(0.5);
        let action = new TWEEN.Tween({scale: 0})
          .to({scale: 2},400)
          .onUpdate(function () {
            sp.scale = {
              x: this.scale,
              y: this.scale
            }
          })
        action.start();



        break;
      default:
        break;
    }

    _state = newState;
  };

  let shake = function (node,index,cb) {
    if (index <= 0){
      if (cb){
        cb();
      }
      return
    }
    index --;
    let pos = node.position;
    let action = new TWEEN.Tween(pos)
      .to({x: pos.x + getRandomNum(pos) * 10,
        y:pos.y  + getRandomNum(pos) * 10},200)
      .onUpdate(function () {
        if (node){
          node.position = {
            x: this.x,
            y: this.y
          }
        }
      })
      .onComplete(()=>{
        shake(node,index,cb);
      });
    action.start();

    let a1 = new TWEEN.Tween({scale: 1})
      .to({scale: 1.2},100)
      .onUpdate(function () {
        node.scale  = {
          x: this.scale,
          y: this.scale
        }
      })
      .onComplete(()=>{
        a2.start();
      });
    a1.start();
    let a2 = new TWEEN.Tween({scale: 1.2})
      .to({scale: 1},100)
      .onUpdate(function () {
        node.scale  = {
          x: this.scale,
          y: this.scale
        }
      })
      .onComplete(()=>{

      });
    a2.start();


  }
  that.isCut = false;
  let _speedY = Math.random() * 400 + 1100;
  let _speedX = Math.random() * 300 - 150;
  if (that.type === 'superFruit'){
    _speedX = 0;
    _speedY = 1300;
  }
  let _acceleration = -( Math.random() * 0.05 + 0.14) * 10;
  let _RotationSpeed = (Math.random() * 6 - 3) * 100;

  let _fruitCell ;
  if (spec.image === 'bomb'){
    _fruitCell = BombCell({
      animate: resources.ani_bomb,
      scale: 1,
      position: Vec2(0,0)
    });
  }else if(spec.image === 'superFruit'){
    _fruitCell = FruitCell({
      image: 'pomegranate',
      scale: spec.scale,
      position: Vec2(0,0)
    });
    setTimeout(()=>{
      _setState(FruitState.SuperFruitBomb);
    },6000);
  }else {
    _fruitCell = FruitCell({
      image: spec.image,
      scale: spec.scale,
      position: Vec2(
        0,0
      ),
      state: spec.state
    });
  }


  _fruitCell.add2World(that);

  _setState(FruitState.Running);
  that.add2World = (parent)=>{
    that.parent = parent;
    that.parent.addChild(that);
  }
  that.move = (dt)=>{
    dt *= _timeScaleOffset;
    if (_state === FruitState.Running || _state === FruitState.CutOver || _state === FruitState.SlowSpeed || _state === FruitState.StopY){
      if (_state === FruitState.SlowSpeed && spec.image === 'bomb'){

        return;
      }
      const dtSecond = dt / 1000;
      _speedY += _acceleration * dt;
      if (spec.image === 'superFruit'){
        if (_state === FruitState.Running){
          if (Math.abs(_speedY) < 100){
            _setState(FruitState.StopY);
          }
        }
      }
      if (_state === FruitState.StopY){
        _speedX = 0 ;
        _speedY = 0;
      }

      let pos = Vec2(that.position.x,that.position.y).sub(Vec2(_speedX * dtSecond,_speedY * dtSecond));
      that.position = {
        x: pos.x,
        y: pos.y
      }

      if (_state === FruitState.Running || _state === FruitState.StopY){
        that.rotation = that.rotation + _RotationSpeed * dtSecond * Math.PI/180;
      }

      if (_speedY < -2000){
        _setState(FruitState.Over);
      }
    }

    if (_fruitCell.update){
      _fruitCell.update(dt);
    }


  }


  that.Cut = (touchVec)=>{
    if (_state === FruitState.Running || _state === FruitState.StopY ){
      let localVec = Vec2(that.position.x,that.position.y);
      let dis = localVec.getDistance(touchVec);
      if (dis < 50){
        // Director.sharedDirector().audio.play('cut_' + spec.image);


        _isCut = true;

        if (that.type === 'bomb'){
          global.audioControl.play('cut_' + spec.image);
          global.playerdata.cutBombCount++;
          global.events.fire('cutbomb');
          global.events.fire('flasheffect',{
            position: localVec,
            node: that
          });

          global.playerdata.lostRedBagCount += global.playerdata.getRealRedbagCount;
          global.playerdata.getRedbagCount = 0;
          global.playerdata.getRealRedbagCount = 0;
          global.playerdata.getRangRedbagSum = 0;///
          let tempScore = global.playerdata.totalScore;
          global.playerdata.totalScore -= global.playerdata.cutBombSubScore;
          if (global.playerdata.totalScore <= 0){
            global.playerdata.totalScore = 0;
          }else {
            tempScore = global.playerdata.cutBombSubScore;

          }
          global.playerdata.cutBombSubScoreList.push(tempScore);

          if (global.playerdata.cutBombSubScore === 0){
            //  0    
          }else {

            global.events.fire('addShow',{
              text: '-' + global.playerdata.cutBombSubScore,
              font: 'rank_number_6',
              position: {
                x: that.position.x,
                y: that.position.y - 100
              }
            });
          }

          _setState(FruitState.Cut);

        }else if (that.type === 'superFruit'){
          // console.log(' ');
          // global.events.fire('zoomIn')
          global.events.fire('shakeCamera');
          cutSuperEffect(that,()=>{
            if (_state === FruitState.SuperFruitBomb){
              return;
            }
            global.audioControl.play('cut_banana');

            _superFruitCutCount ++;
            global.playerdata.cutSuperCount = _superFruitCutCount;
            global.events.fire('addShow',{
              text: '+' + _superFruitCutCount + '',
              font: 'combo',
              position: {
                x: that.position.x,
                y: that.position.y - 100
              }
            });

            _RotationSpeed *= -1;
            global.events.fire('superfuritcut',that.position);
            // console.log('+1');
            global.events.fire('cuteffect',{
              image: 'pomegranate',
              position: localVec.add(Vec2(Math.random() * 200 - 100,Math.random() * 200 -100)),
              direction: touchVec.sub(localVec)
            });
            global.events.fire('fruitemit',{
              pos: touchVec,
              image: 'pomegranate'
            });
            global.events.fire('cellMove',{
              node: that,
              ang: that.rotation,
              pos: {
                x: that.position.x,
                y: that.position.y
              }
            })
            // addCutEffect({x: Math.random() * 100 - 50, y:Math.random() * 100 - 50});
            
          });

        }else {


          global.playerdata.totalScore += 10;
          _setState(FruitState.Cut);
          global.events.fire('cutfruit',{
            position: {
              x: that.position.x,
              y: that.position.y
            }
          });
          global.events.fire('addcutfruit');
          console.log('play sound' + 'cut_' + spec.image);
          global.audioControl.play('cut_' + spec.image);
          //global.playerdata.gold ++;
          global.playerdata.cutFruitCount ++;
          global.events.fire('cuteffect',{
            image: spec.image,
            position: localVec,
            direction: touchVec.sub(localVec)
          });
          global.events.fire('fruitemit',{
            pos: touchVec,
            image: spec.image
          });
          if (that.bigredbag === true){
            console.log('');

            global.playerdata.getRangRedbagSum += global.playerdata.randCentNum;
            global.playerdata.getRealRedbagCount ++;
            global.playerdata.getRedbagCount += global.playerdata.randCentNum;
            global.playerdata.allGetRedbag += global.playerdata.randCentNum;
            // allGetRedbag
          }
          if (that.redbag === true){
            global.playerdata.getRealRedbagCount ++;
            global.playerdata.getRedbagCount ++;
            global.playerdata.allGetRedbag ++;
            global.events.fire('addShow',{
              text: '+1',
              // font: 'gift_number',
              position: {
                x: localVec.x,
                y: localVec.y
              }
            });
            // Director.sharedDirector().audio.play('getred_' + Math.round(Math.random() * 1))
            //global.audioControl.play(defines.musicConfig['getred_' + Math.round(Math.random() * 1)]);
            global.audioControl.play('getred_1');
            global.events.fire('addredbageffect',{
              image: 'redbag',
              position: {
                x: that.position.x,
                y: that.position.y
              }
            });
          }
        }
      }
    }
  }


  // const addCutEffect = function (pos) {
  //   console.log('add Cut effect');
  //   // let effect = PIXI.Sprite.fromImage(resources['daoguang' + Math.round(Math.random() * 1)]);
  //   // that.addChild(effect);
  //   // effect.anchor.set(0.5);
  //   // // effect.position.set(pos);
  //   // effect.rotation = Math.random() * 360 * Math.PI/180;
  //   // let action = new TWEEN.Tween({alpha: 1})
  //   //   .to({alpha: 0},100)
  //   //   .onUpdate(function () {
  //   //     effect.alpha = this.alpha;
  //   //   })
  //   //   .onComplete(function () {
  //   //     that.removeChild(effect);
  //   //   });
  //   //   action.start();
  // }

  const  getRandomNum = (pos)=>{
    let w = Math.random() * 2 - 1;
    let i = w / Math.abs(w);
    // console.log('' + i);

    return i;
  }

  const cutSuperEffect = function (object,cb) {
    if (_isCanCut){

    }else {
      return
    }
    _isCanCut = false;
    let objectPos = object.position;

    let action = new TWEEN.Tween({scale: 1, x: objectPos.x,y: objectPos.y})
      .to({scale: 1.04,
        x: objectPos.x + getRandomNum(objectPos) * 10,
        y: objectPos.y + getRandomNum(objectPos) * 10},Math.random() * 10 + 10)
      .onUpdate(function () {
        object.scale = {
          x: this.scale,
          y: this.scale
        }
        object.position = {
          x: this.x,
          y: this.y
        }
      })
      .onComplete(()=>{
        action1.start();
      });
    action.start();
    let action1 = new TWEEN.Tween({scale: 1.04})
      .to({scale: 1},Math.random() * 40 + 60)
      .onUpdate(function () {
        object.scale = {
          x: this.scale,
          y: this.scale
        }
      })
      .onComplete(()=>{
        _isCanCut = true;
        if (cb){
          cb();
        }

        object.position = objectPos;

      });
  };

  that.isOver = ()=>{
    if (_state === FruitState.Over){
      return true;
    }else {
      return false;
    }
  }



  let SlowSpeed = ()=>{
    // console.log('fruit game over');
    _setState(FruitState.SlowSpeed);
  }
  global.events.on('cutbomb',SlowSpeed);


  that.removeSelf = ()=>{

    _setState(FruitState.Over);
  }


  that.destory = ()=>{

    if (_isCut){

    }else {
      if (that.redbag){
        global.playerdata.lostRedBagCount ++;
      }
      if (that.bigredbag){
        global.playerdata.lostRedBagCount ++;
      }
    }


    _fruitCell.removeSelf();
    that.parent.removeChild(that);
    global.events.removeListener('cutbomb',SlowSpeed);
  }

  return that;
}
export default Fruit;


