import {ResourceManager,AnimationFactor,Vec2} from './../utility/imports'
import resources from './../resources'
const BombCell = (spec)=>{
  let that = new PIXI.Container();

  ////Bomb
  // console.log('spec.image=' + JSON.stringify(spec.animate));
  that.position = {
    x: spec.position.x,
    y: spec.position.y
  }
  ResourceManager.loadList([spec.animate,resources.ani_spark],()=>{
    let animate = AnimationFactor.createAnimation({
      type: 'frame',
      anchor: {x:0.5,y:0.5},
      animates: {
          idle: {
            pre: 'bomb',
            start: 1,
            end: 4,
            speed: 60/60 * 0.25
           }
      }

    });
    that.addChild(animate.node);
    animate.playLoop('idle');

    that.sparkanimate = AnimationFactor.createAnimation({
      type:'frame',
      anchor: {x: 0.5,y: 0.5},
      animates: {
        idle: {
          pre: 'spark',
          start: 1,
          end: 12,
          speed: 60/60 * 0.25
        }
      }
    });
    that.addChild(that.sparkanimate.node);
    that.sparkanimate.playLoop('idle');




    // that.bombSpark = BombSpark({
    //   animate: resources.ani_spark,
    //   scale: 1,
    //   position: Vec2(0,0)
    // });
    // that.bombSpark.add2World(that);
  });





  that.add2World = (parent)=>{
    that.parent = parent;
    that.parent.addChild(that);
  }



  that.removeSelf = ()=>{
    // that.bombSpark.removeSelf();
    that.parent.removeChild(that);
  }

  that.cut = ()=>{

  }
  that.update = (dt)=>{
    if (that.parent){
      if (that.sparkanimate){
        that.sparkanimate.node.rotation = that.parent.rotation * -1;
      }
     // let purPos = Vec2(0,1).rotateByAngle(Vec2(0,0),that.parent.rotation).multValue(30).add(Vec2(10,10));
     // _bombSpark.position = purPos;
    }
  }




  return that;
}
export  default BombCell;


