import {Helper} from './../utility/imports'
import resources from './../resources'
const HeadNode = (data)=>{
  let that = new PIXI.Container();
  let image = "http://image1.yuanfenba.net/uploads/oss/avatar/201601/29/19031717956.png";
  if (data.image){
    image = data.image;
  }
  // let img_url = Helper.getImgUrlWithSize(image, 46);
  // _headIcon.texture = new PIXI.Texture.fromImage(img_url);
  // let imageUrl = Helper.getImgUrlWithSize(data.image,46);
  // that.addChild(new PIXI.Texture.fromImage(imageUrl));
  // let imageUrl = Helper.getImgUrlWithSize(data., 46);

  let headNode = new PIXI.Sprite.fromImage(resources.star);
  headNode.anchor = {
    x: 0.5,
    y: 0.5
  }
  let imageUrl = Helper.getImgUrlWithSize(image,46);
  console.log('imageUrl +++' + imageUrl);
  let mask = new PIXI.Sprite.fromImage(resources.head_white);
  mask.anchor = {
    x: 0.5,
    y: 0.5
  }
  that.addChild(mask);
  that.mask = mask;
  let head = new PIXI.Texture.fromImage(imageUrl);
  headNode.texture = head;
  that.addChild(headNode);
  return that;
}
export default HeadNode;


