import {UIControl,ModelLayer,Inherited,Director,ResourceManager} from './../../utility/imports'
import global from './../../game-global'
import resources from './../../resources'
const Tips = (data,callback)=>{
  let that = Inherited(ModelLayer(true));
  that.inheritOn('init',()=>{
    return true;
  });

  ResourceManager.load(resources.box_white,()=>{
    let _mainNode = UIControl.create({
      id: 'mainNode',
      component: 'Layout',
      position: {
        x: Director.sharedDirector().screen.right * 0.5,
        y: Director.sharedDirector().screen.bottom * 0.5
      },
      children: [
        {
          id: 'bg',
          component: 'Scale9Sprite',
          image: resources.box_white,
          offsetX: 3,
          offsetY: 20,
          tilingWidth: 1,
          tilingHeight: 1,
          width: 340,
          height: 200
        },
        {
          id: 'label1',
          component: 'Label',
          text: '',
          position: {
            x: 0,
            y: -40
          },
          style: {
            font: '20px Arial',
            fill: '#000000'
          }
        },
        {
          id: 'labele2',
          component: 'Label',
          text: data,
          position: {
            x: 0,
            y: 0
          }
        },
        {
          id: 'ok',
          component: 'Button',
          image: resources.zhuangban,
          position: {
            x: 0,
            y: 60
          },
          children:[
            {
              id: 'label',
              component: 'Label',
              text: '',
              style: {
                font: '24px Arial',
                fill: '#ffffff'
              }
            }
          ]


        },

      ]
    },(button)=>{
      if (button.id === 'ok'){

        that.removeFromWorld();


        if (callback){
          callback();
        }



      }
    });

    that.node.addChild(_mainNode.node);

  });



  return that;

}
export default Tips;


