import {SRequest} from './utility/imports'
import defines from './game-defines'
import platform from './platform-helper'
const PlayerData = ()=>{
  let that = {};
  that.uid = 0;

  that.allFruitCount = 0; //
  that.fruitCount = 10;  // 
  that.centsNum = 0;  // 
  that.bombCount = 0; //
  that.gold = 100000;
  that.randCentNum = 0;
  that.cutBombSubScore = 0;///


  that.allGetRedbag = 0; // 
  that.getRedbagCount = 0;//
  that.getRealRedbagCount = 0;//
  that.cutFruitCount = 0;///
  that.cutBombCount = 0;  ///
  that.cutSuperCount = 0;//
  that.totalScore = 0;///,
  that.lostRedBagCount = 0;//
  that.cutBombSubScoreList = [];//;
  that.getRangRedbagSum = 0; ///;
  that.deviceModel = 'none'; ///



  // let userInfo = platform.getUserInfo();
  // let userInfo = {};
  // platform.getUserInfo((data)=>{
  //   userInfo = data;
  // });
  // that.uid = userInfo.uid;
  // that.auth = userInfo.auth;

  // that.oldRankNum = 0;///
  // that.nowRankNum = 0;///
  // that.changedRankNum = 0;//


  that.hitsLists = []; ///
  that.initData = ()=>{
      that.fruitCount = 10;  // 
      that.cutSuperCount = 0;
    that.allGetRedbag = 0; // 
    that.getRedbagCount = 0;//
    that.getRealRedbagCount = 0;//
    that.cutFruitCount = 0;///
    that.cutBombCount = 0;  ///
    that.cutSuperCount = 0;//
    that.totalScore = 0;///
    that.lostRedBagCount = 0;//
    that.cutBombSubScoreList = [];//;
    that.getRangRedbagSum = 0;//;
  };


  that.requestData = (method,params,cb)=>{
    SRequest.post(defines.gameHomeURL,method + '?uid=' + that.uid,{},params,data=>{
      console.log('requestData',method);
      if (data.status !== 'ok'){
        console.error('requestData,Failed!',JSON.stringify(data));
      }else {

      }

      if (cb){
        cb(data);
      }

    });
  }
  that.getGameInfo = (cb)=>{



    platform.safeRequest(defines.gameHomeURL + defines.URLMethods.gameStart,{
      uid: that.uid,
      auth: that.auth,
      channel:  (()=>{
        if (platform.osVer === 1){
          return 'iOS';
        }else if (platform.osVer === 2){
          return 'Android';
        }else {
          return 'none';
        }
      })(),
      deviceType: that.deviceModel
    },(data)=>{
      if (data.status !== 'ok'){

        cb.call(null, false, data);
      }else {

        console.log('gamestart info=' + JSON.stringify(data));

        that.fruitCount = data.res.fruitsNum;
        that.centsNum = data.res.centsNum;
        that.bombCount =data.res.bombNum;
        that.gold = data.res.money_pool;
        that.allFruitCount = data.res.fruitsNum;
        that.randCentNum = data.res.randCentNum;
        that.cutBombSubScore = data.res.bombHarm;//

        // that.fruitCount = 2;
        // that.centsNum = 100;
        // that.bombCount = 0;

        if (cb){
          console.log('run cb: ' + that.cutBombSubScore);
          cb.call(null, true);
        }
      }
    });



    // that.requestData(defines.URLMethods.gameStart,{
    //   uid: that.uid,
    //   auth: that.auth,
    //   channel:  (()=>{
    //     if (platform.osVer === 1){
    //       return 'iOS';
    //     }else if (platform.osVer === 2){
    //       return 'Android';
    //     }else {
    //       return 'none';
    //     }
    //   })()
    // },(data)=>{
    //   if (data.status != 'ok'){
    //
    //     cb.call(null, false, data);
    //   }else {
    //
    //     console.log('gamestart info=' + JSON.stringify(data));
    //
    //     that.fruitCount = data.res.fruitsNum;
    //     that.centsNum = data.res.centsNum;
    //     that.bombCount =data.res.bombNum;
    //     that.gold = data.res.money_pool;
    //     that.allFruitCount = data.res.fruitsNum;
    //     that.randCentNum = data.res.randCentNum;
    //     that.cutBombSubScore = data.res.bombHarm;//
    //
    //     // that.fruitCount = 2;
    //     // that.centsNum = 100;
    //     // that.bombCount = 0;
    //
    //     if (cb){
    //       cb.call(null, true);
    //     }
    //   }
    // });
  }
  that.gameOver = (cb)=>{
    console.log('' + that.cutFruitCount);
    console.log('' + that.cutBombCount);
    console.log('' + that.getRedbagCount);
    console.log('' + (that.allGetRedbag - that.getRedbagCount));
    console.log('' + JSON.stringify(that.hitsLists));
    console.log('' + that.cutSuperCount);
    console.log('' + that.centsNum - that.getRealRedbagCount);
    console.log('' + JSON.stringify(that.cutBombSubScoreList));
    console.log('' + that.getRangRedbagSum);
    //
    // if (cb){
    //   cb({
    //     status: 'fail',
    //     show: ''
    //   })
    // }
    // return
    //// 

    platform.safeRequest(defines.gameHomeURL + defines.URLMethods.gameOver, {
      uid: that.uid,
      bombNum: that.cutBombCount,
      centsNum: that.getRedbagCount,
      fruitsNum: that.cutFruitCount,
      failCentsNum: (that.allGetRedbag - that.getRedbagCount),
      furyCutNum: that.cutSuperCount,
      comboList: that.hitsLists,
      bombHarmList: that.cutBombSubScoreList,
      randCentNum: that.getRangRedbagSum,
      deviceType: that.deviceModel
    },cb);


    // that.requestData(defines.URLMethods.gameOver, {
    //   uid: that.uid,
    //   bombNum: that.cutBombCount,
    //   centsNum: that.getRedbagCount,
    //   fruitsNum: that.cutFruitCount,
    //   failCentsNum: (that.allGetRedbag - that.getRedbagCount),
    //   furyCutNum: that.cutSuperCount,
    //   comboList: that.hitsLists,
    //   bombHarmList: that.cutBombSubScoreList,
    //   randCentNum: that.getRangRedbagSum,
    //   deviceType: that.deviceModel
    // },cb);

  }


  that.getRanklistData = (cb,page,limit)=>{
    that.requestData(defines.URLMethods.GetFruitRank,{
      uid: that.uid,
      page: page,
      limit: limit
    },(data)=>{
      if (data.status === 'ok'){
        if (cb){
          // console.log('getRanklistData :' + JSON.stringify(data));
          cb(data.res);
        }
      }

    });
  }

  return that;
}
export default PlayerData;


