import {BaseLayer,UIControl,Inherited,Vec2,Director} from './../../src/utility/imports'
const Camera = (spec)=>{
  var that = Inherited(BaseLayer());
  let _viewWidth = spec.viewWidth;
  let _viewHeight = spec.viewHeight;
  let _width = spec.maxWidth;
  let _height = spec.maxHeight;


  let _moveLayer = new PIXI.Container();
  that.node.addChild(_moveLayer);

  let _mashLayer = new PIXI.Container();
  that.node.addChild(_mashLayer);


  let _moveLayer1 = new PIXI.Container();
  that.node.addChild(_moveLayer1);

  let _lookList = [];

  that.inheritOn('init',()=>{

    console.log('init camera');
    return true;
  });




  that.mask = (image)=>{
    _mashLayer.addChild(new PIXI.Sprite.fromImage(image));
  }


  if (spec.mashImage){
    that.mask(spec.mashImage);
  }

  that.LookAt = (object)=>{


    if (object.cameraPos){
      that.lookObject = object;
      //that.node.addChild(object.node);
      // _moveLayer1.addChild(object.node);
    }else {
      console.log('no have cameraPos');
    }

  }
  var scaleAction = (object,value,cb,director)=>{
    var action = new TWEEN.Tween({scale: object.scale.x})
      .to({scale: value},300)
      .onUpdate(function () {

        object.position = {
          x : Director.sharedDirector().screen.right * 0.5 * (this.scale - 1) * director,
          y : Director.sharedDirector().screen.bottom * 0.5 * (this.scale -1) * director
        }
        object.scale = {
          x: this.scale,
          y: this.scale
        }
      }).
      onComplete(()=>{
        if (cb){
          cb();
        }
      }).easing(TWEEN.Easing.Cubic.In);
    action.start();
  }
  that.zoomIn = (value)=>{
    // _moveLayer1.scale = {
    //   x: value,
    //   y: value
    // }

    var index = 0;
    var cb = ()=>{
      index ++;
      if (index === _lookList.length){
        console.log('In');
      }
    }
    for (var i = 0 ; i < _lookList.length ; i ++){
      _lookList[i].oldScale = _lookList[i].scale;
      scaleAction(_lookList[i],value,cb,-1);
    }
  };



  that.zoomOut = (value)=>{
    var index = 0;
    for (var i = 0 ; i < _lookList.length ; i ++){
      _lookList[i].scale = _lookList[i].oldScale;
      _lookList[i].position = {
        x: 0,
        y: 0
      }
    }
    // var cb = ()=>{
    //   index ++;
    //   if (index === _lookList.length){
    //     console.log('Out');
    //   }
    // };
    // for (var i = 0 ; i < _lookList.length ; i ++){
    //   scaleAction(_lookList[i],1.2,cb,1);
    // }
  };


  const ShakeAction = (node,value)=>{
    if (node.isAction === false){
      node.isAction = true;
    }else {
      return;
    }
    var targetPosx = node.position.x + value;
    var targetPosx1 = node.position.x - value;
    let action = new TWEEN.Tween({value: node.position.x})
      .to({value: targetPosx},20)
      .onUpdate(function () {
        node.position.x = this.value
      })
      .onComplete(()=>{
        action1.start();
      });
    action.start();
    let action1 = new TWEEN.Tween({value: node.position.x})
      .to({value: targetPosx1},20)
      .onUpdate(function () {
        node.position.x = this.value;
      })
      .onComplete(function () {
        node.isAction = false;
      })

  }

  // const Shake = (node,count)=>{
  //
  //   if (count < 0){
  //     node.position = node.oldPosition;
  //     return;
  //   }
  //   ShakeAction(node,count%2?10:-10,()=>{
  //     count --;
  //     Shake(node,count);
  //   });
  // }
  that.shakeCamera = ()=> {
    console.log('shake camera');
    // for (var i = 0 ; i < _lookList.length ; i ++) {
    //   _lookList[i].oldPosition = _lookList[i].position;
    //   // Shake(_lookList[i],2);
    //   _lookList[i].isAction = false;
    //   ShakeAction(_lookList[i],4);
    // }
  }

  that.addToCameraDeep = (object)=>{
    ////
    _moveLayer.addChild(object);
  }

  that.addToCamera = (object)=>{
    ////
    // _moveLayer1.addChild(object);
    _lookList.push(object);
  }
  that.cameraUpdate = ()=>{

    if (that.lookObject){
      let pos = Vec2(_viewWidth/2,_viewHeight/2).sub(that.lookObject.cameraPos);
      pos.x = Math.min(0,pos.x);
      pos.x = Math.max(_viewWidth - _width,pos.x);
      pos.y = Math.min(0,pos.y);
      pos.y = Math.max(_viewHeight - _height,pos.y);
      _moveLayer.position = {
        x: pos.x,
        y: pos.y
      }
      _moveLayer1.position = {
        x: pos.x,
        y: pos.y
      }


    }else {

    }
  }



  that.getViewRange = ()=>{
    return PIXI.Rectangle(_moveLayer.position.x,_moveLayer.position.y,_viewWidth,_viewHeight);
  }



  // that.inheritOn('update',()=>{
  //   that.cameraUpdate();
  // });

  return that;

}
export default Camera;


