import  OneBlade from './../one-blade'
const BladeState = {
  Invalide: -1,
  Waiting: 0,
  Running: 1,
  Over: 2
}
const BladeRender = (spec)=>{
  let that = {};

  const _onBegin = (event)=> {
    spec.touchBegan();
  };
  let _multPoints = {};
  let _state = BladeState.Invalide;

  const _onMove = (event)=> {
    if (_state != BladeState.Running){
      return
    }


    spec.touchMove(event.data);

    var pos  = {
      x: event.data.getLocalPosition(spec.node).x,
      y: event.data.getLocalPosition(spec.node).y
    };

    if (_multPoints.hasOwnProperty(event.data.identifier)){
      _multPoints[event.data.identifier]._addTailPoint(pos);
    }else {
      let oneBlade = OneBlade(spec);
      oneBlade.init();
      _multPoints[event.data.identifier] = oneBlade;
    }

  };

  const _onEnd = (event)=>{
    spec.touchEnd();
    if (event.data.identifier === 0 || event.data.identifier == undefined){
      return;
    }

    if(_multPoints[event.data.identifier]) {
      _multPoints[event.data.identifier].removeSelf();
    }

  };
  that.init = ()=>{
  };

  that.update=(dt)=>{
    for (let i  in _multPoints){
      _multPoints[i].update(dt);
    }
  };

  (()=>{
    spec.node.on('touchstart', _onBegin)
      .on('mousedown', _onBegin);


    spec.node.on('mouseup', _onEnd)
      .on('touchend', _onEnd)
      .on('mouseupoutside', _onEnd)
      .on('touchendoutside', _onEnd);

    spec.node.on('touchmove', _onMove)
      .on('mousemove', _onMove);
  })();

  let _setState = (state)=>{
    if (state === _state){
      return;
    }
    switch (state){
      case BladeState.Waiting:
        break;
      case BladeState.Running:
        break;
      case BladeState.Over:
        break;
      default:
        break;
    }
    _state = state;
  }

  _setState(BladeState.Waiting);
  that.toStartTouchMove = ()=>{
    _setState(BladeState.Running);
  }
  that.toEndTouchMove = ()=>{
    _setState(BladeState.Over);
  }
  return that;
};
export default BladeRender;



