const fpsLimits = 1000 / 60;
import TWEEN from 'tween.js'
import Registry from '../registry'
const Director = function (width, height) {
  let that = {};

  that.width = width;
  that.height = height;

  that.screen = {
    scale: 1,
    left: 0,
    right: width,
    top: 0,
    bottom: height
  };

  //that.renderer = new PIXI.CanvasRenderer(width, height);
  that.renderer = PIXI.autoDetectRenderer(width, height);
  document.body.appendChild(that.renderer.view);

  let _worldList = [];

  const _getTopWorld = ()=> {
    if(_worldList.length > 0) {
      return _worldList[_worldList.length - 1];
    }

    return null;
  };

  let _lastTime = performance.now();
  var stepOnce = (timestamp)=> {
    requestAnimationFrame(stepOnce);

    // that.audio.update();
    var dt = timestamp - _lastTime;
    //if (dt < fpsLimits) {
    //  return;
    //}

    //if (dt = fpsLimits)

    if (!_getTopWorld() ) {
      _lastTime = timestamp;
      return;
    }

    var tmpTime = _lastTime;
    while (dt > fpsLimits) {
      tmpTime += fpsLimits;
      PIXI.ticker.shared.update(tmpTime);
      TWEEN.update(tmpTime);

      that.updateExecute(fpsLimits);

      dt -= fpsLimits;
    }

    tmpTime += dt;
    PIXI.ticker.shared.update(tmpTime);
    TWEEN.update(tmpTime);

    that.updateExecute(dt);

    that.renderer.render(_getTopWorld().node);

    if(that.onLoopEnd) {
      that.onLoopEnd.call(this)
    }

    _lastTime = timestamp;
  };
  stepOnce(_lastTime);
  
  that.popWorld = ()=> {
    let lastWorld = _worldList.pop();
    if (lastWorld) {
      lastWorld.onPause();
      lastWorld.destroy();
    }

    let topWorld = _getTopWorld();
    if(topWorld) {
      topWorld.onResume();
    }
  };

  that.replaceWorld = (newWorld)=> {
    let topWorld = _worldList.pop();
    if(topWorld) {
      topWorld.onPause();
      topWorld.destroy();
    }

    _worldList.push(newWorld);
    newWorld.onResume();

    window.dispatchEvent(new Event('resize'));

  };

  that.startWorld = (newWorld)=> {

    let topWorld = _getTopWorld();
    if(topWorld) {
      topWorld.onPause();
    }

    _worldList.push(newWorld);
    newWorld.onResume();

    window.dispatchEvent(new Event('resize'));
  };

  Registry(that,['update']);

  Object.defineProperties(that, {
    'runningWorld': {
      get: ()=> {
        return _getTopWorld();
      }
    }
  });

  return that;
};


let SharedDirector = SharedDirector || (()=> {
    let that = {};
    let sharedDirector = null;
    that.create = function (width, height) {
      sharedDirector = Director(width, height);
      return sharedDirector;
    };

    that.sharedDirector = function () {
      return sharedDirector;
    };

    return that;

  })();

export  default SharedDirector;





