import global from './../game-global'
import defines from './../game-defines'
const DebugLayer = ()=>{
 let that = new PIXI.Container();
  global.events.on('debug',(data)=>{
    //console.log('data' + JSON.stringify(data));
    addLog(data);


  });


  // let _versionText = new PIXI.Text(global.playerdata.uid + '/' + global.playerdata.auth,{
  //   font: '24px',
  //   fill: '#FFFFFF',
  // });
  let _versionText = new PIXI.Text(defines.versionCode + ':' + global.playerdata.uid,{
      font: '24px',
      fill: '#FFFFFF',
    });
  _versionText.position = {
    x: 20,
    y: 0
  }
  // that.addChild(_versionText);




  let _texts = [];
  let fixLength = ()=>{
    if (_texts.length > 20){
      that.removeChild(_texts[0]);
      _texts.splice(0,1);
      for (let i = 0 ;i < _texts.length ; i ++){
        _texts[i].position.y = 50 + i * 24
      }
      fixLength();
    }else {

    }
  }
  let addLog = (str)=>{
    let text = new PIXI.Text(str,{
      fill: '#ffffff'
    });
    that.addChild(text);
    text.position.x = 0;
    if (_texts.length === 0 ){
      text.position.y = 50;
    }else {
      text.position.y = _texts[_texts.length - 1].position.y + 24;
    }
    _texts.push(text);
    fixLength();


  }






  return that;
}
export default DebugLayer;


